import * as React from 'react';
import { NO_ACTIVE_TOOLBAR, useThisStyles } from '../main/Constant';

interface SubItemToolbar {
  id: number
  label: string
  action(): void
}

interface ItemToolbar {
  id: number
  label: string
  subitems: SubItemToolbar[]
}

export interface ToolbarProps {
  items: ItemToolbar[]
}

export default function Toolbar(props: ToolbarProps) {

  const style = useThisStyles();
  const [active_id, setActiveID] = React.useState<number>(NO_ACTIVE_TOOLBAR);

  React.useEffect(() => {
    const clickOutsideHandler = () => {
      if (active_id !== NO_ACTIVE_TOOLBAR) {
        setActiveID(NO_ACTIVE_TOOLBAR);
      }
    }
    window.addEventListener('mousemove', clickOutsideHandler);
    return () => {
      window.removeEventListener('mousemove', clickOutsideHandler);
    }
  })

  return (
    <div 
      className={style.toolbar_container}
      onMouseMove={e => {
        e.preventDefault();
        e.stopPropagation();
      }}>
      {
        props.items.map((item: ItemToolbar) => (
          <div key={item.id} className={style.toolbar_item_container} style={{ position: 'relative' }}>
            <div 
              className={style.toolbar_item}
              onMouseMove={e => {
                e.preventDefault();
                e.stopPropagation();
                setActiveID(item.id);
              }}>
              { item.label }
            </div>
            { active_id === item.id && <div className={style.toolbar_container_subitem}>
              {
                item.subitems.map((subitem: SubItemToolbar) => (
                  <div 
                    key={subitem.id}
                    onClick={_ => subitem.action()}
                    className={style.toolbar_subitem}>
                    { subitem.label }
                  </div>
                ))
              }
            </div> }
          </div>
        ))
      }
    </div>
  );
}
