import * as React from 'react';
import BaseEditorData from '../main/base-editor/BaseEditorData';
import BaseEditor, { BaseEditorProps, baseMemoFunction } from './../main/base-editor/BaseEditor';
import TextareaAutosize from 'react-textarea-autosize';
import APIVariable, { APIVariableData } from './APIVariableView';
import { getBuiltinTypeColor, getNextType, getPrevType, NO_VARIABLE_TYPE_PARENT, VariableType } from '../main/Constant';

export interface EditorAPIData extends BaseEditorData {
  method: string
  url: string
  variables: APIVariableData[]
  return_type: VariableType
}

export interface EditorAPIProps extends BaseEditorProps<EditorAPIData> {
}

function EditorAPI(props: EditorAPIProps) {
  return (
    <BaseEditor {...props}>
      <div style={{ padding: '4px 12px', paddingTop: 10, fontSize: 10 }}>
        <select 
          style={{ 
            width: '100%', 
            background: 'rgba(255, 255, 255, .36)', 
            borderRadius: 4,
            border: 'none',
            outline: 'none',
            color: 'white',
            padding: '2px 4px'
          }}
          value={props.data.method}
          disabled={props.isReadOnly}
          onChange={(e: React.ChangeEvent<HTMLSelectElement>) => { 
            const { value } = e.target;
            props.onValueChange({
              method: value
            });
          }}>
          <option value="get">GET</option>
          <option value="post">POST</option>
          <option value="put">PUT</option>
          <option value="delete">DELETE</option>
        </select>
      </div>
      <div style={{ padding: '0px 12px' }}>
          <TextareaAutosize 
              cols={2}
              placeholder="endpoint"
              value={props.data.url}
              disabled={props.isReadOnly}
              style={{ 
                  outline: 'none',
                  border: 'none',
                  borderRadius: 4,
                  padding: '8px 12px',
                  fontFamily: 'inherit', 
                  boxSizing: 'border-box',
                  fontSize: 10, 
                  width: '100%', 
                  color: 'white', 
                  background: '#1e1e1e' 
              }}
              onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => { 
                  const { value } = e.target;
                  // setUrl(value);
                  props.onValueChange({
                    url: value
                  });
              }}
              />
      </div>
      <div>
        {
          props.data.variables.map((variable: APIVariableData) => (
            <APIVariable 
              isReadOnly={props.isReadOnly}
              key={variable.id} 
              data={variable}
              listEditor={props.listEditor ?? []}
              dependencies={props.data.dependencies}
              setName={(name: string) => {
                props.onValueChange({
                  variables: props.data.variables.map(_var => {
                    if (_var.id === variable.id) {
                      return { ...variable, name }
                    }
                    return _var;
                  })
                });
              }}
              setType={(type: string) => {
                props.onValueChange({
                  variables: props.data.variables.map(_var => {
                    if (_var.id === variable.id) {
                      return { ...variable, type }
                    }
                    return _var;
                  })
                });
              }}
              setRequired={(required: boolean) => {
                props.onValueChange({
                  variables: props.data.variables.map(_var => {
                    if (_var.id === variable.id) {
                      return { ...variable, required }
                    }
                    return _var;
                  })
                });
              }}
              onRemove={() => {
                props.onValueChange({
                  variables: [...props.data.variables.filter(_v => _v.id !== variable.id)]
                });
              }}
            />
          ))
        }
      </div>
      { !props.isReadOnly && <div style={{ display: 'flex', flexDirection: 'row', justifyContent: '', margin: '4px 12px', borderRadius: 4, background: 'rgba(255, 255, 255, .4)' }}>
        <div 
          style={{ width: '25%', boxSizing: 'border-box', padding: 3, textAlign: 'center', outline: 'none' }}
          tabIndex={0}
          onKeyDown={undefined}
          role="menuitem"
          onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
            props.onValueChange({
              variables: [...props.data.variables, {
                id: new Date().getTime(),
                kind: 'H',
                name: '',
                type: {
                  type_name: 'any',
                  type_parent_id: NO_VARIABLE_TYPE_PARENT
                },
                required: true
              }]
            });
          }}>
          +Header
        </div>
        <div 
          style={{ width: '25%', boxSizing: 'border-box', padding: 3, textAlign: 'center', outline: 'none', borderRight: 'solid 1px rgba(255, 255, 255, .4)', borderLeft: 'solid 1px rgba(255, 255, 255, .4)' }}
          tabIndex={0}
          onKeyDown={undefined}
          role="menuitem"
          onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
            props.onValueChange({
              variables: [...props.data.variables, {                        
                id: new Date().getTime(),
                kind: 'Q',
                name: '',
                type: {
                  type_name: 'any',
                  type_parent_id: NO_VARIABLE_TYPE_PARENT
                },
                required: true
              }]
            });
          }}>
          +Query
        </div>
        <div 
          style={{ width: '25%', boxSizing: 'border-box', padding: 3, textAlign: 'center', outline: 'none', borderRight: 'solid 1px rgba(255, 255, 255, .4)' }}
          tabIndex={0}
          onKeyDown={undefined}
          role="menuitem"
          onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
            props.onValueChange({
              variables: [...props.data.variables, {                        
                id: new Date().getTime(),
                kind: 'B',
                name: '',
                type: {
                  type_name: 'any',
                  type_parent_id: NO_VARIABLE_TYPE_PARENT
                },
                required: true
              }]
            });
          }}>
          +Body
        </div>
        <div 
          style={{ width: '25%', boxSizing: 'border-box', padding: 3, textAlign: 'center', outline: 'none' }}
          tabIndex={0}
          onKeyDown={undefined}
          role="menuitem"
          onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
            props.onValueChange({
              variables: [...props.data.variables, {
                id: new Date().getTime(),
                kind: 'P',
                name: '',
                type: {
                  type_name: 'any',
                  type_parent_id: NO_VARIABLE_TYPE_PARENT
                },
                required: true
              }]
            });
          }}>
          +Param
        </div>
      </div> }
      <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center', margin: '0px 12px', marginTop: 8 }}>
        <div style={{ marginRight: 4 }}>
          return
        </div>
        <div 
          onDoubleClick={e => { e.preventDefault(); e.stopPropagation(); }} 
          style={{ 
            outline: 'none', 
            boxSizing: 'border-box', 
            background: 'white',
            fontWeight: 'bold', 
            textAlign: 'center', 
            flexGrow: 1, 
            padding: '4px 12px', 
            borderRadius: 4,
            color: getBuiltinTypeColor(props.data.return_type)
          }}
          tabIndex={0}
          onKeyDown={undefined}
          role="menuitem"
          onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
            if (props.isReadOnly) {
              return;
            }
            props.onValueChange({
              return_type: (
                e.shiftKey
                ? getPrevType(props.data.return_type, props.listEditor, props.data.dependencies)
                : getNextType(props.data.return_type, props.listEditor, props.data.dependencies)
              )
            });
          }}>
          { props.data.return_type.type_name }
        </div>
      </div>
    </BaseEditor>
  );
}

export default React.memo(
  EditorAPI, 
  function (prevProps: Readonly<EditorAPIProps>, nextProps: Readonly<EditorAPIProps>): boolean {
      let result = baseMemoFunction<EditorAPIData>(prevProps, nextProps);
      if (prevProps.data.method !== nextProps.data.method) {
          return false;
      }
      if (prevProps.data.url !== nextProps.data.url) {
          return false;
      }
      if (prevProps.data.return_type !== nextProps.data.return_type) {
          return false;
      }
      if (prevProps.data.variables.length !== nextProps.data.variables.length) {
          return false;
      }
      for (let i = 0; i < prevProps.data.variables.length; i += 1) {
          const pv = prevProps.data.variables[i];
          const nv = nextProps.data.variables[i];
          if (pv.name !== nv.name) {
              return false;
          }
          if (pv.type !== nv.type) {
              return false;
          }
          if (pv.required !== nv.required) {
              return false;
          }
      }

      return result;
  }
);
