import TextareaAutosize from 'react-textarea-autosize';
import * as React from 'react';
import { useThisStyles } from '../Constant';

export interface HeaderEditorProps {
  title: string
  description: string
  isReadOnly: boolean

  setTitle(title: string): void
  setDescription(description: string): void
  onRequestDelete(): void
}

export default function HeaderEditor(props: HeaderEditorProps) {
  const style = useThisStyles();
  return (
    <div>
      <div className={style.header_editor_title_container}>
        <TextareaAutosize
          spellCheck={false}
          cols={1}
          placeholder="Card / Function Name"
          className={style.header_editor_title}
          disabled={props.isReadOnly}
          value={props.title} 
          onChange={e => props.setTitle(e.target.value)} />
        { !props.isReadOnly && <button style={{
          border: 'none',
          background: 'rgba(255, 255, 255, .3)',
          outline: 'none',
          padding: '4px 8px'
        }} onClick={() => props.onRequestDelete()}>
          <img style={{height: 9, width: 9}} src={'/close.png'} />
        </button> }
      </div>
      <div className={style.header_editor_description_container}>
        <TextareaAutosize
          spellCheck={false}
          cols={1}
          placeholder="Add Description"
          disabled={props.isReadOnly}
          className={style.header_editor_description}
          value={props.description} 
          onChange={e => props.setDescription(e.target.value)} />
      </div>
    </div>
  );
}