import * as React from 'react';
import ContentEditable from 'react-contenteditable';
import { useThisStyles } from '../main/Constant';

export interface ItemSheet {
  id_sheet: number
  title: string
  is_active: boolean
}

export interface BottomBarProps {
  setReadOnly(): void
  onRequestNewSheet(): void
  onRequestChangeSheet(id_sheet: number): void
  onRequestRemoveSheet(id_sheet: number): void
  onChangeSheetName(id_sheet: number, name: string): void
  listSheet: ItemSheet[]
  isReadOnly: boolean
}

export default function BottomBar(props: BottomBarProps) {

  const style = useThisStyles();
  const [is_edit_mode, setEditMode] = React.useState<boolean>(false);

  React.useEffect(() => {
    const clickOutsideHandler = () => {
      if (is_edit_mode) {
        setEditMode(false);
      }
    }
    window.addEventListener('click', clickOutsideHandler);
    return () => {
      window.removeEventListener('click', clickOutsideHandler);
    }
  })

  return (
    <div 
      className={style.bottom_bar_container}
      onDoubleClick={e => { e.preventDefault(); e.stopPropagation(); }}>
      <div style={{
        display: 'flex',
        flexDirection: 'row'
      }}>
        {
          props.listSheet.map((item: ItemSheet) => (
            <div 
              className={style.bottom_bar_item}
              key={item.id_sheet} 
              onClick={e => {
                e.stopPropagation();
                props.onRequestChangeSheet(item.id_sheet);
              }}
              style={{
                transform: `translateY(${item.is_active ? 1 : 6}px)`,
              }}>
              <ContentEditable
                style={{
                  outline: 'none'
                }}
                onClick={e => setEditMode(true)}
                onDoubleClick={e => {
                  e.preventDefault();
                  e.stopPropagation();
                }}
                onKeyPress={e => e.key === 'Enter' && setEditMode(false)}
                html={item.title}
                disabled={!is_edit_mode}
                onChange={e => props.onChangeSheetName(item.id_sheet, e.target.value)}
              />
              <div 
                style={{
                  marginLeft: 8,
                }}
                onClick={e => {
                  e.preventDefault();
                  e.stopPropagation();
                  props.onRequestRemoveSheet(item.id_sheet);
                }}>
                <img style={{height: 9, width: 9}} src={'/close.png'} />
              </div>
            </div>
          ))
        }
        <div
          className={style.bottom_bar_item}
          style={{
            transform: `translateY(6px)`,
          }}
          onClick={_ => props.onRequestNewSheet()}>
          +
        </div>
      </div>
      <div style={{
        padding: '7px 8px',
        marginBottom: -8
      }}>
        <button
          className={style.bottom_bar_item}
          style={{ border: 'none', outline: 'none' }}
          onDoubleClick={e => { e.preventDefault(); e.stopPropagation(); }}  
          onClick={_ => props.setReadOnly()}>
          { props.isReadOnly ? 'Read Only' : 'Edit Mode' }
        </button>
      </div>
    </div>
  );
}
