import * as React from 'react';
import BaseEditorData from '../main/base-editor/BaseEditorData';
import { getBuiltinTypeColor, getNextType, getPrevType, useThisStyles, VariableType } from '../main/Constant';

export interface APIVariableData {
  id: number
  kind: string
  name: string
  type: VariableType
  required: boolean
}

export interface APIVariableProps {
  isReadOnly: boolean
  data: APIVariableData
  listEditor: BaseEditorData[]
  dependencies: number[]
  setName: Function
  setType: Function
  setRequired: Function
  onRemove: Function
}

export default function APIVariable(props: APIVariableProps) {
  const style = useThisStyles();
  return (
    <div 
      onDoubleClick={e => { e.preventDefault(); e.stopPropagation(); }} 
      style={{ margin: '4px 12px', display: 'flex', flexDirection: 'row', background: 'rgba(255, 255, 255, .2)', color: 'white', alignItems: 'center', borderRadius: 4, overflow: 'hidden' }}>
      <div style={{ padding: '0px 4px', outline: 'none', color: 'white', marginLeft: 1 }}>
        { props.data.kind }
      </div>
      <input 
        className={style.general_input}
        style={{ flexGrow: 1, width: '100%', outline: 'none' }} 
        placeholder="name" 
        type="text" 
        disabled={props.isReadOnly}
        value={props.data.name}
        onChange={((e: React.ChangeEvent<HTMLInputElement>) => {
          props.setName!(e.target.value);
        })}
        />
      <div 
        style={{ 
          padding: '4px 4px', 
          outline: 'none',
          background: 'rgba(255, 255, 255, .7)',
          color: getBuiltinTypeColor(props.data.type)
        }}
        tabIndex={0}
        onKeyDown={undefined}
        role="menuitem"
        onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
          if (props.isReadOnly) {
            return;
          }
          props.setType!(
            e.shiftKey
            ? getPrevType(props.data.type, props.listEditor ?? [], props.dependencies ?? [])
            : getNextType(props.data.type, props.listEditor ?? [], props.dependencies ?? [])
          );
        }}>
        { props.data.type.type_name }
      </div>
      <div 
        tabIndex={0}
        onKeyDown={undefined}
        role="menuitem"
        onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
          if (props.isReadOnly) {
            return;
          }
          props.setRequired!(!props.data.required);
        }}
        style={{ padding: '4px 4px', background: props.data.required ? '#333' : '#BBB', outline: 'none' }}>
        { props.data.required ? '!' : '?' }
      </div>
      { !props.isReadOnly && <div 
        style={{ padding: '4px 4px', background: 'rgba(255, 255, 255, .2)', color: 'white', outline: 'none' }}
        tabIndex={0}
        onKeyDown={undefined}
        role="menuitem"
        onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
          props.onRemove!();
        }}>
        X
      </div> }
    </div>
  );
}