import * as React from 'react';
import BaseEditor, { BaseEditorProps, baseMemoFunction } from './../main/base-editor/BaseEditor';
import { getBuiltinTypeColor, getBuiltinTypes, addArrayToEachType, getPrevType, getNextType, VariableType, NO_VARIABLE_TYPE_PARENT, useThisStyles } from './../main/Constant';
import { ModelVariable, Variable } from './VariableView';
import BaseEditorData from '../main/base-editor/BaseEditorData';
import { UIEvent, UIState } from './UIStateView';
import { LAMBDA_TYPE } from '../main/ComponentConstant';

export interface EditorUIData extends BaseEditorData {
  list_ui_parameter: ModelVariable[]
  list_ui_state: UIState[]
  list_ui_event: UIEvent[]
}

export function UIStateView(props: UIState) {
  const style = useThisStyles();
  return (
    <div style={{ margin: '4px 12px', display: 'flex', flexDirection: 'row', color: 'white', alignItems: 'center', borderRadius: 4, overflow: 'hidden' }}>
      <input 
        className={style.general_input}
        type="text" 
        value={props.name}
        disabled={props.isReadOnly}
        onChange={((e: React.ChangeEvent<HTMLInputElement>) => {
          props.setName!(e.target.value);
        })}
        />
      <div 
        style={{ 
          padding: '4px 4px', 
          outline: 'none', 
          background: 'rgba(255, 255, 255, .7)',
          color: getBuiltinTypeColor(props.type)
        }}
        tabIndex={0}
        onKeyDown={undefined}
        role="menuitem"
        onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
          if (props.isReadOnly) {
            return;
          }
          props.setType!(
            e.shiftKey
            ? getPrevType(props.type, props.listEditor ?? [], props.dependencies ?? [])
            : getNextType(props.type, props.listEditor ?? [], props.dependencies ?? [])
          );
        }}>
        { props.type.type_name }
      </div>
      { !props.isReadOnly && <div 
        style={{ padding: '4px 4px', background: 'rgba(255, 255, 255, .2)', color: 'white', outline: 'none' }}
        tabIndex={0}
        onKeyDown={undefined}
        role="menuitem"
        onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
          props.onRemove!();
        }}>
        X
      </div> }
    </div>
  );
}

export interface EditorUIProps extends BaseEditorProps<EditorUIData> {}

function EditorUI(props: EditorUIProps) {
  function getAllPossibleFunction(): { key: number, value: string }[] {
    const list_editor: BaseEditorData[] = props.listEditor;
    const list_editor_ids: number[] = list_editor.map((editor: BaseEditorData) => editor.id);
    const list_editor_ids_lambda: number[] = list_editor.filter((editor: BaseEditorData) => editor.item_type === LAMBDA_TYPE).map((editor: BaseEditorData) => editor.id);
    return [
      ...props.data.dependencies
      .filter((editor_id: number) => list_editor_ids.includes(editor_id))
      .map((editor_id: number) => list_editor.find((editor: BaseEditorData) => editor.id === editor_id)!)
      .map((editor: BaseEditorData) => ({ key: editor.id, value: editor.title})),
      ...props.data.list_ui_parameter
      .filter((param: ModelVariable) => (param.type.type_name === 'function' || list_editor_ids_lambda.includes(param.type.type_parent_id)))
      .map((param: ModelVariable) => ({ key: param.id, value: `Property ${param.name}` }))
    ];
  }

  return (
    <BaseEditor {...props}>
      <div style={{
          paddingTop: 5,
          paddingBottom: 7
      }}>
        <div>
          { (props.isReadOnly && props.data.list_ui_parameter.length > 0) && <div style={{ padding: '0px 12px' }}>
            Property
          </div> }
          {
            props.data.list_ui_parameter.map((x: ModelVariable) => (
              <Variable
                {...x} 
                key={x.id} 
                isReadOnly={props.isReadOnly}
                listEditor={props.listEditor}
                dependencies={props.data.dependencies}
                setName={(name: string) => {
                  props.onValueChange({
                    list_ui_parameter: props.data.list_ui_parameter.map(_var => {
                      if (_var.id === x.id) {
                        return { ...x, name }
                      }
                      return _var;
                    })
                  });
                }}
                setType={(type: VariableType) => {
                  props.onValueChange({
                    list_ui_parameter: props.data.list_ui_parameter.map(_var => {
                      if (_var.id === x.id) {
                        return { ...x, type }
                      }
                      return _var;
                    })
                  });
                }}
                setRequired={(required: boolean) => {
                  props.onValueChange({
                    list_ui_parameter: props.data.list_ui_parameter.map(_var => {
                      if (_var.id === x.id) {
                        return { ...x, required }
                      }
                      return _var;
                    })
                  });
                }}
                onRemove={() => {
                  props.onValueChange({
                    list_ui_parameter: [...props.data.list_ui_parameter.filter(_v => _v.id !== x.id)]
                  });
                }}
              />
          ))
          }
          { !props.isReadOnly && <div style={{ display: 'flex', flexDirection: 'row', justifyContent: '', margin: '4px 12px', border: 'solid 1px rgba(255, 255, 255, .1)', borderRadius: 4, background: 'rgba(255, 255, 255, .6)', color: '#333' }}>
            <div 
              style={{ width: '100%', boxSizing: 'border-box', padding: 3, textAlign: 'center', outline: 'none' }}
              tabIndex={0}
              onKeyDown={undefined}
              role="menuitem"
              onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
                props.onValueChange({
                  list_ui_parameter: [...props.data.list_ui_parameter, {
                    id: new Date().getTime(),
                    name: '',
                    type: {
                      type_parent_id: NO_VARIABLE_TYPE_PARENT, 
                      type_name: 'any'
                    },
                    required: true
                  }]
                });
              }}>
              + Property
            </div>
          </div> }
          <div style={{
            margin: '10px 0px',
            height: 1,
            background: 'rgba(0, 0, 0, .15)'
          }} />
          { (props.isReadOnly && props.data.list_ui_state.length > 0) && <div style={{ padding: '0px 12px' }}>
            State
          </div> }
            {
              props.data.list_ui_state.map((x: UIState) => (
                <UIStateView
                  {...x} 
                  key={x.id} 
                  isReadOnly={props.isReadOnly}
                  listEditor={props.listEditor ?? []}
                  dependencies={props.data.dependencies}
                  setName={(name: string) => {
                    props.onValueChange({
                      list_ui_state: props.data.list_ui_state.map(_var => {
                        if (_var.id === x.id) {
                          return { ...x, name }
                        }
                        return _var;
                      })
                    });
                  }}
                  setType={(type: string) => {
                    props.onValueChange({
                      list_ui_state: props.data.list_ui_state.map(_var => {
                        if (_var.id === x.id) {
                          return { ...x, type }
                        }
                        return _var;
                      })
                    });
                  }}
                  onRemove={() => {
                    props.onValueChange({
                      list_ui_state: [...props.data.list_ui_state.filter(_v => _v.id !== x.id)]
                    });
                  }}
                />
              ))
            }
            { !props.isReadOnly && <div style={{ display: 'flex', flexDirection: 'row', justifyContent: '', margin: '4px 12px', border: 'solid 1px rgba(255, 255, 255, .1)', borderRadius: 4, background: 'rgba(255, 255, 255, .6)', color: '#333' }}>
              <div 
                style={{ width: '100%', boxSizing: 'border-box', padding: 3, textAlign: 'center', outline: 'none' }}
                tabIndex={0}
                onKeyDown={undefined}
                role="menuitem"
                onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
                  props.onValueChange({
                    list_ui_state: [...props.data.list_ui_state, {
                      id: new Date().getTime(),
                      name: '',
                      type: {
                        type_parent_id: NO_VARIABLE_TYPE_PARENT, 
                        type_name: 'any'
                      },
                      required: true
                    }]
                  });
                }}>
                + State
              </div>
            </div> }
            <div style={{
              margin: '10px 0px',
              height: 1,
              background: 'rgba(0, 0, 0, .15)'
            }} />
            { (props.isReadOnly && props.data.list_ui_event.length > 0) && <div style={{ padding: '0px 12px' }}>
              Event
            </div> }

            {
              props.isReadOnly && props.data.list_ui_event.map((ui_event: UIEvent, index: number) => (
                <div key={index} style={{ 
                  margin: '6px 12px', 
                  display: 'flex',
                  flexDirection: 'column'
                }}>
                  <div style={{
                    fontWeight: 'bold'
                  }}>
                    { ui_event.object_name }
                  </div>
                  <div>
                    { ui_event.type }  { getAllPossibleFunction().find((fx) => fx.key == ui_event.editor_id_action)?.value ?? "<unset>" }
                  </div>
                </div>
              ))
            }
            {
              !props.isReadOnly && props.data.list_ui_event.map((ui_event: UIEvent, index: number) => (
                <div key={index} style={{ 
                  margin: '6px 12px', 
                  display: 'flex'
                }}>
                  <div style={{
                    fontSize: 10, 
                    borderRadius: 4,
                    borderTopRightRadius: 0
                  }}>
                    <div style={{
                      padding: '4px 0px',
                      paddingTop: 0,
                    }}>
                      <input 
                        style={{ 
                          boxSizing: 'border-box', 
                          flexGrow: 1, 
                          width: '100%', 
                          outline: 'none', 
                          padding: '4px 6px', 
                          background: 'rgba(255, 255, 255, .7)',
                          border: 'none', 
                          borderRadius: 4,
                          borderBottomLeftRadius: 0,
                          borderBottomRightRadius: 0,
                          borderTopRightRadius: 0,
                          fontSize: 12
                        }} 
                        disabled={props.isReadOnly}
                        placeholder="object name" 
                        type="text" 
                        value={ui_event.object_name}
                        onChange={((e: React.ChangeEvent<HTMLInputElement>) => {
                          const { value } = e.target;
                          let new_value = props.data.list_ui_event.map(x => ({...x}));
                          new_value[index].object_name = value;
                          props.onValueChange({
                            list_ui_event: new_value
                          });
                        })}
                      />
                      <input 
                        style={{ 
                          boxSizing: 'border-box', 
                          flexGrow: 1, 
                          width: '100%', 
                          background: 'rgba(255, 255, 255, .5)',
                          border: 'none', 
                          outline: 'none', 
                          fontSize: 12,
                          padding: '2px 6px', 
                        }} 
                        placeholder="event type" 
                        type="text" 
                        disabled={props.isReadOnly}
                        value={ui_event.type}
                        onChange={((e: React.ChangeEvent<HTMLInputElement>) => {
                          const { value } = e.target;
                          let new_value = props.data.list_ui_event.map(x => ({...x}));
                          new_value[index].type = value;
                          props.onValueChange({
                            list_ui_event: new_value
                          });
                        })}
                      />
                      <select 
                        style={{ 
                          width: '100%', 
                          background: 'rgba(255, 255, 255, .36)', 
                          borderRadius: 4,
                          borderTopLeftRadius: 0,
                          borderTopRightRadius: 0,
                          fontSize: 12,
                          border: 'none',
                          outline: 'none',
                          padding: '2px 2px'
                        }}
                        value={ui_event.editor_id_action}
                        disabled={props.isReadOnly}
                        onChange={(e: React.ChangeEvent<HTMLSelectElement>) => { 
                          const { value } = e.target;
                          let new_value = props.data.list_ui_event.map(x => ({...x}));
                          new_value[index].editor_id_action = parseInt(value);
                          props.onValueChange({
                            list_ui_event: new_value
                          });
                        }}>
                        <option value={null} selected>Choose action</option>
                        {
                          getAllPossibleFunction().map((fx, index_2: number) => (
                            <option key={index_2} value={fx.key}>{ fx.value }</option>
                          ))
                        }
                      </select>
                    </div>
                  </div>
                  { !props.isReadOnly && <div style={{
                    outline: 'none',
                  }}>
                    <div 
                      style={{ 
                        padding: '4px 4px', 
                        background: 'rgba(255, 255, 255, .2)', 
                        color: 'white', 
                        outline: 'none',
                        borderTopRightRadius: 4,
                        borderBottomRightRadius: 4
                      }}
                      tabIndex={0}
                      onKeyDown={undefined}
                      role="menuitem"
                      onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
                        props.onValueChange({
                          list_ui_event: [...props.data.list_ui_event.filter(_v => _v.id !== ui_event.id)]
                        });
                      }} >
                      X
                    </div>
                  </div> }
                </div>
              ))
            }
        </div>
        { !props.isReadOnly && <div style={{ display: 'flex', flexDirection: 'row', justifyContent: '', margin: '4px 12px', border: 'solid 1px rgba(255, 255, 255, .1)', borderRadius: 4, background: 'rgba(255, 255, 255, .6)', color: '#333' }}>
          <div 
            style={{ width: '100%', boxSizing: 'border-box', padding: 3, textAlign: 'center', outline: 'none' }}
            tabIndex={0}
            onKeyDown={undefined}
            role="menuitem"
            onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
              props.onValueChange!({
                list_ui_event: [...props.data.list_ui_event, {
                  id: (new Date()).getTime(),
                  object_name: '',
                  type: '',
                  editor_id_action: ''
                }]
              });
            }}>
            + Event
          </div>
        </div> }
      </div>
    </BaseEditor>
  );
}

export default React.memo(
  EditorUI, 
  function (prevProps: Readonly<EditorUIProps>, nextProps: Readonly<EditorUIProps>): boolean {
    let result = baseMemoFunction<EditorUIData>(prevProps, nextProps);
    if (prevProps.data.list_ui_event.length !== nextProps.data.list_ui_event.length) {
      return false;
    }

    for (let i = 0; i < prevProps.data.list_ui_event.length; i += 1) {
      const a = prevProps.data.list_ui_event[i];
      const b = nextProps.data.list_ui_event[i];
      if (a.editor_id_action !== b.editor_id_action) {
        return false;
      }
      if (a.object_name !== b.object_name) {
        return false;
      }
      if (a.type !== b.type) {
        return false;
      }
    }

    if (prevProps.data.list_ui_state.length !== nextProps.data.list_ui_state.length) {
      return false;
    }
    
    for (let i = 0; i < prevProps.data.list_ui_state.length; i += 1) {
      const pv = prevProps.data.list_ui_state[i];
      const nv = nextProps.data.list_ui_state[i];
      if (pv.name !== nv.name) {
        result = false;
        break;
      }
      if (pv.type !== nv.type) {
        result = false;
        break;
      }
    }

    if (prevProps.data.list_ui_parameter.length !== nextProps.data.list_ui_parameter.length) {
      return false;
    }
    
    for (let i = 0; i < prevProps.data.list_ui_parameter.length; i += 1) {
      const pv = prevProps.data.list_ui_parameter[i];
      const nv = nextProps.data.list_ui_parameter[i];
      if (pv.name !== nv.name) {
        result = false;
        break;
      }
      if (pv.type !== nv.type) {
        result = false;
        break;
      }
      if (pv.required !== nv.required) {
        result = false;
        break;
      }
    }
    
    return result;
  }
);
