import * as React from 'react';
import BaseEditorData from '../main/base-editor/BaseEditorData';
import { API_ACCESS, BUSINESS_LOGIC, DATA_ACCESS, EXTERNAL_MODULE, LAMBDA_TYPE, MODEL_DATA, PRESENTATION_API, PRESENTATION_UI, PRESENTATION_UI_COMPONENTS } from '../main/ComponentConstant';
import { groupBy, NO_ACTIVE_EDITOR, useThisStyles } from '../main/Constant';
import { ContextMenuItem } from '../main/context-menu/ContextMenu';
import { RegisterComponent } from '../main/RegisterComponent';

export interface CollectionListProps {
  listEditor: BaseEditorData[]
  activeEditorID: number
  onEditorClick(editor: BaseEditorData): void
}

export default function CollectionList(props: CollectionListProps) {
  const style = useThisStyles();
  const grouped: {[key: number]: BaseEditorData[]} = groupBy(props.listEditor, 'item_type');
  const editor_focus = props.listEditor.find((editor: BaseEditorData) => editor.id === props.activeEditorID);
  const collection_order = [PRESENTATION_UI, PRESENTATION_UI_COMPONENTS, PRESENTATION_API, BUSINESS_LOGIC, API_ACCESS, DATA_ACCESS, MODEL_DATA, LAMBDA_TYPE, EXTERNAL_MODULE];
  const item_register_components: {[key: number]: ContextMenuItem} = collection_order.reduce((acc: {[key: number]: ContextMenuItem}, curr: number) => ({
    ...acc,
    [curr]: RegisterComponent.find(x => x.id === curr)
  }), {});

  return (
    <div 
      className={style.collection_list_container}
      onWheel={e => e.stopPropagation()}
      onMouseDown={e => e.stopPropagation()}
      onMouseMove={e => e.stopPropagation()}
      onMouseUp={e => e.stopPropagation()}
      onDoubleClick={e => e.stopPropagation()}>
      {
        collection_order.filter(key => grouped.hasOwnProperty(key)).map(key => (
          <div key={key} className={style.collection_list_main_item}>
            <div className={style.collection_list_main_item_title}>
              {
                item_register_components[key]?.label ?? "-"
              }
            </div>
            <div 
              className={style.collection_list_main_item_sub_list}>
              {
                grouped[key]?.map((editor: BaseEditorData) => (
                  <div
                    style={{
                      borderColor: props.activeEditorID === editor.id ? 'white' : 'transparent',
                      opacity: props.activeEditorID === NO_ACTIVE_EDITOR ? .8 : ((props.activeEditorID === editor.id || (editor_focus?.dependencies.includes(editor.id) ?? false)) ? 1 : 0.4),
                      background: item_register_components[key]?.point_color ?? ''
                    }}
                    onClick={_ => props.onEditorClick(editor)}
                    className={style.collection_list_main_item_sub_list_item}
                    key={editor.id}>
                    <div style={{
                      flexGrow: 1,
                      wordBreak: 'break-all',
                      padding: '3px 10px'
                    }}>
                      { editor.title ? editor.title : '<unnamed>' }
                    </div>
                  </div>
                )) ?? <div />
              }
            </div>
          </div>
        ))
      }
    </div>
  );
}