const mainIndex = list_editor => `
require('dotenv').config();
const express = require('express')
const app = express();

import { validationResult } from 'express-validator';
import { check } from 'express-validator';
import { Router } from 'express';
import cors from 'cors';
${importDependencyMain(list_editor)}

export function validate(request: any, response: any, next: any) {
  const error = validationResult(request);
  if (!error.isEmpty()) {
    const error_string: string = error.array().map(cur => \`\${cur.msg} \${cur.param}\`).join(',');
    next(new Error(error_string));
  }
  next();
}

export function response_ok(response: any, data: any, code: number = 200) {
  response.status(code).json({
    status: true,
    data
  });
}

export function response_error(response: any, message: string, code: number = 400) {
  response.status(code).json({
    status: false,
    message
  });
}

(async () => {
  app.use(express.json());
  app.use('/static', express.static('public'));
  app.use(cors());

  ${createRouter(list_editor)}
  app.use('/', router);
  app.use((error: Error, request, response, next) => {
    console.error(error.message);
    response_error(response, error.message);
  });

  app.listen(process.env.PORT, () => console.log(\`listening at http://localhost:\${process.env.PORT}\`));
})();
`

function createRouter(list_editor: BaseEditorData[]): string {
  const list_model: number[] = list_editor.filter((editor: BaseEditorData) => editor.item_type === MODEL_DATA).map(x => x.id);
  return `const router = Router();
${list_editor.filter((editor: BaseEditorData) => editor.item_type === 3).map((editor: EditorAPIData) => `
  router.${editor.method}(
    '${editor.url}', 
    [\n${editor.variables.map((_var: APIVariableData) => {
      const map_var = {
        'any': 'isAscii()',
        'string': 'isString()',
        'number': 'isNumeric()',
        'any[]': 'isArray()',
        'string[]': 'isArray()',
        'number[]': 'isArray()',
      }

      return `      check('${_var.name}').${_var.required ? '' : 'optional().'}${map_var[getDataType(list_editor, _var.type)]},`;
    })
    .join('\n')}
    ], 
    validate,
    (async (request, response, next) => {
      try {
        const params = {
${editor.variables.map((_var: APIVariableData) => {
  const map_var = {
    'B': 'body',
    'Q': 'query',
    'P': 'params'
  };
  return `          ${_var.name}: request.${map_var[_var.kind]}.${_var.name}`
}).join(',\n')}
        };
        response_ok(response, await ${
          (() => {
            for (let i = 0; i < editor.dependencies.length; i++) {
              if (list_model.includes(editor.dependencies[i])) {
                continue;
              }
              return list_editor.find(x => x.id === editor.dependencies[i]).title;
            }
            return "/** not found function name **/";
          })()
        }(params));
      } catch (error) {
        response_error(response, error.toString());
      }
    })
  );
`).join('')}`
}

function importDependencyMain(list_editor: BaseEditorData[]): string {
  const list_model: number[] = list_editor.filter((editor: BaseEditorData) => editor.item_type === MODEL_DATA).map(x => x.id);
  return list_editor
    .filter((editor: BaseEditorData) => {
      return editor.item_type === PRESENTATION_API;
    })
    .map((editor: BaseEditorData) => {
      for (let i = 0; i < editor.dependencies.length; i++) {
        if (list_model.includes(editor.dependencies[i])) {
          continue;
        }
        return list_editor.find(x => x.id === editor.dependencies[i]);
      }
      return null;
    })
    .filter(x => x !== null)
    .map((_editor: BaseEditorData) => {
      const item_context_menu = exporter[_editor.item_type];
      return `import { ${_editor.title} } from './${item_context_menu.exportPath}/${_editor.title}'`
    }).join("\n");
}

function importDependency<T extends BaseEditorData>(_list_editor: T[], editor: T): string {
  const this_item_context_menu = exporter[editor.item_type];
  if (!this_item_context_menu) {
    throw new Error(`Context Menu editor: ${editor.title} tidak ditemukan`);
  }

  const list_editor = _list_editor.filter((editor: BaseEditorData) => exporter[editor.item_type]);

  const folder_depth = (this_item_context_menu.exportPath.match(/\//g) || []).length + 1;
  const list_editor_id = list_editor.map((editor: T) => editor.id);
  
  return editor.dependencies
    .filter((id_dependency: number) => list_editor_id.includes(id_dependency)) // filter only available dependency
    .map((id_dependency: number) => list_editor.find((ed: T) => ed.id === id_dependency)!) // map to editor instance
    .map((_editor: T) => {
      const item_context_menu = exporter[_editor.item_type];
      return `import { ${_editor.title} } from '${'../'.repeat(folder_depth)}${item_context_menu.exportPath}/${_editor.title}'`
    }).join("\n");
}

function createFile<T extends BaseEditorData>(editor: T, all_editor: T[]): string {
  const item = exporter[editor.item_type];
  return item.exportToFile(editor, all_editor);
}

export default function _export_backend_expressjs(list_sheet: IDESheet[], callback: (file: Blob) => void) {
  const jszip = new JSZip();
  const all_editor: BaseEditorData[] = list_sheet.reduce((acc: BaseEditorData[], current: IDESheet) => {
    return [...acc, ...current.list_editor];
  }, []);

  jszip.file('.env', 'PORT=3000');
  jszip.file('.gitignore', gitignore);
  jszip.file('tsconfig.json', tsconfig);
  jszip.file('package.json', packageJSON);
  jszip.file('src/index.ts', mainIndex(all_editor));
  for (let i = 0; i < all_editor.length; i++) {
    const editor = all_editor[i];
    if (editor.item_type === PRESENTATION_API || !exporter[editor.item_type]) {
      continue;
    }
    const item_context_menu = exporter[editor.item_type];
    const dependency_lines = importDependency(all_editor, editor);

    const file_content = `${dependency_lines ? `${dependency_lines}\n\n` : ''}${createFile(editor, all_editor)}\n`;
    jszip.file(`src/${item_context_menu.exportPath}/${editor.title}.${item_context_menu.exportExtension}`, file_content);
  }
  jszip.generateAsync({type:"blob"}).then(callback);
}
