import * as React from 'react';
import { useThisStyles } from '../main/Constant';

interface ItemSideBar {
  id: number
  icon_url: string
  label: string
  action(): void
}

export interface SideBarProps {
  items: ItemSideBar[]
}

export default function SideBar(props: SideBarProps) {

  const style = useThisStyles();

  return (
    <div 
      onClick={e => { e.stopPropagation(); }}
      onDoubleClick={e => { e.preventDefault(); e.stopPropagation(); }}
      className={style.sidebar_container}>
      {
        props.items.map((item: ItemSideBar) => (
          <div 
            className={style.sidebar_item}
            onClick={_ => item.action()}>
            <img className={style.sidebar_item_image} src={item.icon_url} />
          </div>
        ))
      }
    </div>
  );
}
