import JSZip from "jszip";
import { EditorAPIAccessData } from "../components/editor-api-access/EditorAPIAccess";
import { APIVariableData } from "../components/editor-api/APIVariableView";
import { EditorAPIData } from "../components/editor-api/EditorAPI";
import { EditorBusinessLogicData } from "../components/editor-business-logic/EditorBusinessLogic";
import { EditorDataAccessData } from "../components/editor-data-access/EditorDataAccess";
import { EditorDataModelData } from "../components/editor-data-model/EditorDataModel";
import { EditorLambdaTypeData } from "../components/editor-lambda-type/EditorLambdaType";
import { ModelVariable } from "../components/editor-ui/VariableView";
import BaseEditorData from "../components/main/base-editor/BaseEditorData";
import { API_ACCESS, BUSINESS_LOGIC, DATA_ACCESS, EXTERNAL_MODULE, LAMBDA_TYPE, MODEL_DATA, PRESENTATION_API } from "../components/main/ComponentConstant";
import { IDESheet, NO_VARIABLE_TYPE_PARENT, VariableType } from "../components/main/Constant";
import { ExportData } from "./ExportData";


function getDataType(list_editor: BaseEditorData[], data_type: VariableType) {
  if (data_type.type_parent_id === NO_VARIABLE_TYPE_PARENT) {
    return data_type.type_name === 'function' ? '(_params: any) => {}' : data_type.type_name;
  }
  const editor = list_editor.find(x => x.id === data_type.type_parent_id)!;
  if (editor.item_type === LAMBDA_TYPE) {
    return `(${(editor as EditorLambdaTypeData).parameters.map((param: ModelVariable) => {
      return `${param.name}: ${param.type.type_name}`
    }).join(', ')}) => ${(editor as EditorLambdaTypeData).return_type.type_name}`
  }
  return data_type.type_name === "number" ? '' : data_type.type_name;
}


function includeModels<T extends BaseEditorData>(_list_editor: T[], editor: T): string {
  const this_item_context_menu = exporter[editor.item_type];
  if (!this_item_context_menu) {
    throw new Error(`Context Menu editor: ${editor.title} tidak ditemukan`);
  }

  const list_editor = _list_editor.filter((editor: BaseEditorData) => editor.item_type === MODEL_DATA);
  const list_editor_id = list_editor.map((editor: T) => editor.id);
  
  return editor.dependencies
    .filter((id_dependency: number) => list_editor_id.includes(id_dependency)) // filter only available dependency
    .map((id_dependency: number) => list_editor.find((ed: T) => ed.id === id_dependency)!) // map to editor instance
    .map((_editor: T) => {
      const item_context_menu = exporter[_editor.item_type];
      return `use App\\Models\\${_editor.title};`
    }).join("\n");
}

function includeFunctions<T extends BaseEditorData>(_list_editor: T[], editor: T): string {
  const this_item_context_menu = exporter[editor.item_type];
  if (!this_item_context_menu) {
    throw new Error(`Context Menu editor: ${editor.title} tidak ditemukan`);
  }

  const list_editor = _list_editor.filter((editor: BaseEditorData) => editor.item_type !== MODEL_DATA);

  const folder_depth = (this_item_context_menu.exportPath.match(/\//g) || []).length + 1;
  const list_editor_id = list_editor.map((editor: T) => editor.id);
  
  return editor.dependencies
    .filter((id_dependency: number) => list_editor_id.includes(id_dependency)) // filter only available dependency
    .map((id_dependency: number) => list_editor.find((ed: T) => ed.id === id_dependency)!) // map to editor instance
    .map((_editor: T) => {
      const item_context_menu = exporter[_editor.item_type];
      return `  $${_editor.title} = include('../${item_context_menu.exportPath}/${_editor.title}.php');`
    }).join("\n");
}

const exporter: {[key: number]: ExportData } = {
  [EXTERNAL_MODULE]: {
    exportPath: 'app/Http/Controllers/external',
    exportExtension: 'php',
    exportToFile(editor: BaseEditorData, all_editor: BaseEditorData[]) {
      return `<?php\n\n${includeModels(all_editor, editor)}\n\nreturn function() {\n\n${includeFunctions(all_editor, editor)}\n\n  // implement your external module below\n  return null;\n};\n`;
    }
  },
  [PRESENTATION_API]: {
    exportPath: 'app/Http/Controllers/router',
    exportExtension: 'php',
    exportToFile(editor: EditorAPIData, all_editor: BaseEditorData[]) {
      const parameters = editor.variables.map((param: APIVariableData) => `  $${param.name} = $request->${param.name};`).join('\n');
      const validators = editor.variables.map((param: APIVariableData) => `      '${param.name}' => '${param.required ? 'required' : ''}'`).join(',\n');
      return `<?php\n\n${includeModels(all_editor, editor)}\nuse Illuminate\\Http\\Request;\n\nreturn function(Request $request) {\n  $validate = include('../app/Http/Controllers/response/validate.php');\n  $responseOk = include('../app/Http/Controllers/response/ok.php');\n  $responseError = include('../app/Http/Controllers/response/error.php');\n${includeFunctions(all_editor, editor)}\n\n${parameters}\n\n  try {\n    $validate($request, [\n${validators}\n    ]);\n\n    // implement your code below\n    throw new Exception('${editor.title} is not implemented yet');\n    return $responseOk(null);\n  } catch (Exception $e) {\n    return $responseError($e);\n  }\n};\n`;
    },
  },
  [BUSINESS_LOGIC]: {
    exportPath: 'app/Http/Controllers/logic',
    exportExtension: 'php',
    exportToFile(editor: EditorBusinessLogicData, all_editor: BaseEditorData[]) {
      const parameters = editor.parameters.map((param: ModelVariable) => `${getDataType(all_editor, param.type)} $${param.name}${param.required ? '' : ' = NULL'}`).join(', ');
      return `<?php\n\n${includeModels(all_editor, editor)}\n\nreturn function(${parameters}) {\n${includeFunctions(all_editor, editor)}\n\n  // implement your external module below\n  throw new Exception('${editor.title} is not implemented yet');\n};\n`;
    }
  },
  [API_ACCESS]: {
    exportPath: 'app/Http/Controllers/access-api',
    exportExtension: 'php',
    exportToFile(editor: EditorAPIAccessData, all_editor: BaseEditorData[]) {
      const parameters = editor.variables.map((param: APIVariableData) => `${getDataType(all_editor, param.type)} $${param.name}${param.required ? '' : ' = NULL'}`).join(', ');
      return `<?php\n\n${includeModels(all_editor, editor)}\n\nreturn function(${parameters}) {\n${includeFunctions(all_editor, editor)}\n\n  // implement your external module below\n  throw new Exception('${editor.title} is not implemented yet');\n};\n`;
    }
  },
  [DATA_ACCESS]: {
    exportPath: 'app/Http/Controllers/access-data',
    exportExtension: 'php',
    exportToFile(editor: EditorDataAccessData, all_editor: BaseEditorData[]) {
      const parameters = editor.parameters.map((param: ModelVariable) => `${getDataType(all_editor, param.type)} $${param.name}${param.required ? '' : ' = NULL'}`).join(', ');
      return `<?php\n\n${includeModels(all_editor, editor)}\n\nreturn function(${parameters}) {\n${includeFunctions(all_editor, editor)}\n\n  // implement your external module below\n  throw new Exception('${editor.title} is not implemented yet');\n};\n`;
    }
  },
  [MODEL_DATA]: {
    exportPath: 'app/Models',
    exportExtension: 'php',
    exportToFile(editor: EditorDataModelData, all_editor: BaseEditorData[]) {
      return `<?php\n\nnamespace App\\Models;\n\nuse Illuminate\\Database\\Eloquent\\Model;\n\nclass ${editor.title} extends Model {\n  protected $table = '${editor.title}';\n}\n`;
    }
  }
}

function createRoutesFile(all_editor: BaseEditorData[]): string {
  return `<?php\n\nuse Illuminate\\Http\\Request;\nuse Illuminate\\Support\\Facades\\Route;\
  \n\n${
    all_editor
    .filter((editor: BaseEditorData) => editor.item_type === PRESENTATION_API)
    .map((editor: EditorAPIData) => {
      const item_context_menu = exporter[PRESENTATION_API];
      return `Route::${editor.method}('${editor.url}', function (Request $request) {\n  $${editor.title} = include('../${item_context_menu.exportPath}/${editor.title}.php');\n  return $${editor.title}($request);\n});`;
    })
    .join('\n')
  }\n`;
}

function createMigrationsFile(all_editor: BaseEditorData[]): { key: string, value: string }[] {
  const date = new Date();
  const year = date.getFullYear();
  const month = date.getMonth();
  const day = date.getDate();

  const variableMap = {
    any: 'text',
    string: 'string',
    number: 'integer',
    boolean: 'boolean'
  };

  return all_editor
    .filter((editor: BaseEditorData) => editor.item_type === MODEL_DATA)
    .map((editor: EditorDataModelData) => {
      return {
        key: `database/migrations/${year}_${month}_${day}_000000_create_${editor.title}_table.php`,
        value: `<?php

use Illuminate\\Database\\Migrations\\Migration;
use Illuminate\\Database\\Schema\\Blueprint;
use Illuminate\\Support\\Facades\\Schema;

class Create${editor.title}Table extends Migration
{
  /**
   * Run the migrations.
   *
   * @return void
   */
  public function up()
  {
    Schema::create('${editor.title}', function (Blueprint $table) {
${editor.attributes.map((attr: ModelVariable) => attr.name === 'id' ? '      $table->id();' : `      $table->${variableMap[attr.type.type_name]}('${attr.name}')${attr.required ? '' : '->nullable()'};`).join('\n')}
      $table->timestamps();
    });
  }

  /**
   * Reverse the migrations.
   *
   * @return void
   */
  public function down()
  {
    Schema::dropIfExists('${editor.title}');
  }
}
`
      }
    });
} 

export default async function _export_backend_php(list_sheet: IDESheet[], callback: (file: Blob) => void) {
  const jszip = new JSZip();
  const all_editor: BaseEditorData[] = list_sheet.reduce((acc: BaseEditorData[], current: IDESheet) => {
    return [...acc, ...current.list_editor];
  }, []);

  const _file = await (await fetch('/test3.zip')).blob();
  const data = await JSZip.loadAsync(_file);
  
  for (let index in data.files) {
    const key = data.files[index];
    jszip.file(key.name, await key.async("blob"))
  }

  for (let i = 0; i < all_editor.length; i++) {
    const editor = all_editor[i];
    if (!exporter[editor.item_type]) {
      continue;
    }

    const item = exporter[editor.item_type];
    jszip.file(`${item.exportPath}/${editor.title}.${item.exportExtension}`, item.exportToFile(editor, all_editor));
  }
  jszip.file('routes/api.php', createRoutesFile(all_editor));

  const migrations = createMigrationsFile(all_editor);
  for (let i = 0; i < migrations.length; i++) {
    jszip.file(migrations[i].key, migrations[i].value);
  }

  jszip.generateAsync({ type: "blob" }).then(callback);
}
