import * as React from 'react';
import { ModelVariable, Variable } from '../editor-ui/VariableView';
import BaseEditorData from '../main/base-editor/BaseEditorData';
import { getBuiltinTypeColor, getNextType, getPrevType, NO_VARIABLE_TYPE_PARENT, VariableType } from '../main/Constant';
import BaseEditor, { BaseEditorProps, baseMemoFunction } from './../main/base-editor/BaseEditor';

export interface EditorDataAccessData extends BaseEditorData {
  parameters: ModelVariable[]
  return_type: VariableType
}

export interface EditorDataAccessProps extends BaseEditorProps<EditorDataAccessData> {}

function EditorDataAccess(props: EditorDataAccessProps) {
  return (
    <BaseEditor {...props}>
      <div>
      {
        props.data.parameters.map((x: ModelVariable) => (
          <Variable
            {...x} 
            key={x.id} 
            isReadOnly={props.isReadOnly}
            listEditor={props.listEditor ?? []}
            dependencies={props.data.dependencies}
            setName={(name: string) => {
              props.onValueChange({
                parameters: props.data.parameters.map(_var => {
                  if (_var.id === x.id) {
                    return { ...x, name }
                  }
                  return _var;
                })
              });
            }}
            setType={(type: VariableType) => {
              props.onValueChange({
                parameters: props.data.parameters.map(_var => {
                  if (_var.id === x.id) {
                    return { ...x, type }
                  }
                  return _var;
                })
              });
            }}
            setRequired={(required: boolean) => {
              props.onValueChange({
                parameters: props.data.parameters.map(_var => {
                  if (_var.id === x.id) {
                    return { ...x, required }
                  }
                  return _var;
                })
              });
            }}
            onRemove={() => {
              props.onValueChange({
                parameters: [...props.data.parameters.filter(_v => _v.id !== x.id)]
              });
            }}
          />
        ))
      }
    </div>
    { !props.isReadOnly && <div style={{ display: 'flex', flexDirection: 'row', justifyContent: '', margin: '4px 12px', border: 'solid 1px rgba(255, 255, 255, .1)', borderRadius: 4, background: 'rgba(255, 255, 255, .6)', color: '#333' }}>
      <div 
        style={{ width: '100%', boxSizing: 'border-box', padding: 3, textAlign: 'center', outline: 'none' }}
        tabIndex={0}
        onKeyDown={undefined}
        role="menuitem"
        onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
          props.onValueChange({
            parameters: [...props.data.parameters, {                        
              id: new Date().getTime(),
              name: '',
              type: {
                type_name: 'any',
                type_parent_id: NO_VARIABLE_TYPE_PARENT
              },
              required: true
            }]
          });
        }}>
        + Parameter
      </div>
    </div> }
    <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center', margin: '0px 12px', marginTop: 8 }}>
      <div style={{ marginRight: 4 }}>
        return
      </div>
      <div 
        onDoubleClick={e => { e.preventDefault(); e.stopPropagation(); }} 
        style={{ 
          outline: 'none', 
          boxSizing: 'border-box', 
          fontWeight: 'bold', 
          textAlign: 'center', 
          flexGrow: 1, 
          padding: '4px 12px', 
          background: 'white',
          borderRadius: 4,
          color: getBuiltinTypeColor(props.data.return_type)
        }}
        tabIndex={0}
        onKeyDown={undefined}
        role="menuitem"
        onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
          if (props.isReadOnly) {
            return;
          }
          props.onValueChange({
            return_type: (
              e.shiftKey
              ? getPrevType(props.data.return_type, props.listEditor, props.data.dependencies)
              : getNextType(props.data.return_type, props.listEditor, props.data.dependencies)
            )
          });
        }}>
        { props.data.return_type.type_name }
      </div>
    </div>
    </BaseEditor>
  );
}

export default React.memo(
  EditorDataAccess, 
  function (prevProps: Readonly<EditorDataAccessProps>, nextProps: Readonly<EditorDataAccessProps>): boolean {
    let result = baseMemoFunction<EditorDataAccessData>(prevProps, nextProps);
    if (prevProps.data.return_type !== nextProps.data.return_type) {
      return false;
    }
    if (prevProps.data.parameters.length !== nextProps.data.parameters.length) {
      return false;
    }
    for (let i = 0; i < prevProps.data.parameters.length; i += 1) {
      const pv = prevProps.data.parameters[i];
      const nv = nextProps.data.parameters[i];
      if (pv.name !== nv.name) {
        return false;
      }
      if (pv.type !== nv.type) {
        return false;
      }
      if (pv.required !== nv.required) {
        return false;
      }
    }

    return result;
  }
);
