import * as React from 'react';
import { ModelVariable, Variable } from '../editor-ui/VariableView';
import BaseEditorData from '../main/base-editor/BaseEditorData';
import { NO_VARIABLE_TYPE_PARENT, VariableType } from '../main/Constant';
import BaseEditor, { BaseEditorProps, baseMemoFunction } from './../main/base-editor/BaseEditor';

export interface EditorDataModelData extends BaseEditorData {
  attributes: ModelVariable[]
}

export interface EditorDataModelProps extends BaseEditorProps<EditorDataModelData> {}

function EditorDataModel(props: EditorDataModelProps) {
  return (
    <BaseEditor {...props}>
      <div>
        {
          props.data.attributes.map((x: ModelVariable) => (
            <Variable 
              {...x} 
              key={x.id} 
              isReadOnly={props.isReadOnly}
              listEditor={props.listEditor ?? []}
              parentID={props.data.id}
              dependencies={props.data.dependencies}
              setName={(name: string) => {
                props.onValueChange({
                  attributes: props.data.attributes.map(_var => {
                    if (_var.id === x.id) {
                      return { ...x, name }
                    }
                    return _var;
                  })
                });
              }}
              setType={(type: VariableType) => {
                props.onValueChange({
                  attributes: props.data.attributes.map(_var => {
                    if (_var.id === x.id) {
                      return { ...x, type }
                    }
                    return _var;
                  })
                });
              }}
              setRequired={(required: boolean) => {
                props.onValueChange({
                  attributes: props.data.attributes.map(_var => {
                    if (_var.id === x.id) {
                      return { ...x, required }
                    }
                    return _var;
                  })
                });
              }}
              onRemove={() => {
                props.onValueChange({
                  attributes: [...props.data.attributes.filter(_v => _v.id !== x.id)]
                });
              }}
            />
          ))
        }
      </div>
      { !props.isReadOnly && <div style={{ display: 'inline-block', flexDirection: 'row', justifyContent: '', margin: '4px 12px', border: 'solid 1px rgba(255, 255, 255, .1)', borderRadius: 4, background: 'rgba(255, 255, 255, .6)', color: '#333' }}>
        <div 
          style={{ boxSizing: 'border-box', padding: '3px 5px', textAlign: 'center', outline: 'none' }}
          tabIndex={0}
          onKeyDown={undefined}
          role="menuitem"
          onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
            props.onValueChange({
              attributes: [...props.data.attributes, {
                id: new Date().getTime(),
                name: '',
                type: {
                  type_name: 'any',
                  type_parent_id: NO_VARIABLE_TYPE_PARENT
                },
                required: true
              }]
            });
          }}>
          +
        </div>
      </div> }
    </BaseEditor>
  );
}

export default React.memo(
  EditorDataModel, 
  function (prevProps: Readonly<EditorDataModelProps>, nextProps: Readonly<EditorDataModelProps>): boolean {
      let result = baseMemoFunction<EditorDataModelData>(prevProps, nextProps);
      if (prevProps.data.attributes.length !== nextProps.data.attributes.length) {
          return false;
      }
      for (let i = 0; i < prevProps.data.attributes.length; i += 1) {
          const pv = prevProps.data.attributes[i];
          const nv = nextProps.data.attributes[i];
          if (pv.name !== nv.name) {
              return false;
          }
          if (pv.type !== nv.type) {
              return false;
          }
          if (pv.required !== nv.required) {
              return false;
          }
      }

      return result;
  }
);
