import * as React from 'react';
import BaseEditorData from '../base-editor/BaseEditorData';
import { useThisStyles } from '../Constant';

export interface ContextMenuItem {
  id: number
  label: string
  model(props: any): JSX.Element
  initial_props(props: BaseEditorData): any
  point_color: string
}

export interface ContextMenuProps {
  items: ContextMenuItem[]
  onMenuClick: Function
}

export default function ContextMenu(props: ContextMenuProps) {

  const style = useThisStyles();

  return (
    <div className={style.context_menu_container}>
      {
        props.items.map((item: ContextMenuItem) => (
          <div
            className={style.context_menu_item}
            role="menuitem" 
            onKeyDown={undefined} 
            tabIndex={0} 
            onClick={e => props.onMenuClick(item)} 
            key={item.id}>
            <div style={{
              width: 8,
              height: 8,
              borderRadius: 8,
              marginTop: -2,
              background: item.point_color,
              marginRight: 8
            }} />
            { item.label }
          </div>
        ))
      }
    </div>
  );
}
