import * as React from 'react';
import BaseEditorData from '../main/base-editor/BaseEditorData';
import { getBuiltinTypeColor, getNextType, getPrevType, useThisStyles, VariableType } from '../main/Constant';

export interface ModelVariable {
    id: number
    name: string
    type: VariableType
    required: boolean

    isReadOnly: boolean
    parentID?: number
    listEditor: BaseEditorData[]
    dependencies?: number[]
    setName?: Function
    setType?(type: VariableType): void
    setRequired?: Function
    onRemove?: Function
}

export function Variable(props: ModelVariable) {
    const style = useThisStyles();

    return (
        <div
            onDoubleClick={e => { e.preventDefault(); e.stopPropagation(); }} 
            style={{ margin: '4px 12px', display: 'flex', flexDirection: 'row', color: 'white', alignItems: 'center', borderRadius: 4, overflow: 'hidden' }}>
            <input 
                className={style.general_input}
                type="text" 
                disabled={props.isReadOnly}
                value={props.name}
                onChange={((e: React.ChangeEvent<HTMLInputElement>) => {
                    props.setName!(e.target.value);
                })}
                />
            <div 
                style={{ 
                    padding: '4px 4px', 
                    outline: 'none', 
                    background: 'rgba(255, 255, 255, .7)',
                    color: getBuiltinTypeColor(props.type)
                }}
                tabIndex={0}
                onKeyDown={undefined}
                role="menuitem"
                onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
                    if (props.isReadOnly) {
                        return;
                    }
                    props.setType!(
                        e.shiftKey
                        ? getPrevType(props.type, props.listEditor, [...props.dependencies ?? [], props.parentID!])
                        : getNextType(props.type, props.listEditor, [...props.dependencies ?? [], props.parentID!])
                    );
                }}>
                { props.type.type_name }
            </div>
            <div 
                tabIndex={0}
                onKeyDown={undefined}
                role="menuitem"
                onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
                    if (props.isReadOnly) {
                        return;
                    }
                    props.setRequired!(!props.required);
                }}
                style={{ padding: '4px 4px', background: props.required ? '#333' : '#BBB', outline: 'none' }}>
                { props.required ? '!' : '?' }
            </div>
            { !props.isReadOnly && <div 
                style={{ padding: '4px 4px', background: 'rgba(255, 255, 255, .2)', color: 'white', outline: 'none' }}
                tabIndex={0}
                onKeyDown={undefined}
                role="menuitem"
                onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
                    props.onRemove!();
                }}>
                X
            </div> }
        </div>
    );
}
