import TextareaAutosize from 'react-textarea-autosize';
import * as React from 'react';
import BaseEditorData from './BaseEditorData';
import { groupBy, keyOf, useThisStyles } from '../Constant';
import BaseEditor from './BaseEditor';
import { RegisterComponent } from '../RegisterComponent';

export interface DependencyListProps {
  parentID: number
  dependencies: number[]
  listEditor: BaseEditorData[]
  isReadOnly: boolean
  onTopOffsetDependecyChange(key: string, top_offset: number): void
  onRequestRemoveDependency(id_provider: number, id_consumer: number): void
  onDependencyClick(editor: BaseEditorData): void
}

export default function DependencyList(props: DependencyListProps) {

  const style = useThisStyles();
  const list_dependency_instance = props.dependencies.map((id: number) => props.listEditor.find((editor: BaseEditorData) => editor.id === id)).filter(x => !!x);
  const group_dependency: {[key: number]: BaseEditorData[]} = groupBy(list_dependency_instance, 'item_type');
  const group_dependency_ordered: {[key: number]: BaseEditorData} = {};
  Object.keys(group_dependency).sort().forEach(function(key) {
    group_dependency_ordered[key] = group_dependency[key];
  });

  return (
    <div className={style.dependency_list_container}>
      {
        Object.keys(group_dependency_ordered).map(key => (
          <div key={key} className={style.dependency_list_main_item}>
            <div className={style.dependency_list_main_item_title}>
              {
                RegisterComponent.find(x => x.id === parseInt(key))?.label ?? "-"
              }
            </div>
            <div className={style.dependency_list_main_item_list}>
              {
                group_dependency_ordered[key].map((editor: BaseEditorData) => (
                  <div 
                    ref={el => {
                      if (el) {
                        props.onTopOffsetDependecyChange(keyOf(props.parentID, editor.id), el!.offsetTop + (el!.offsetHeight / 2));
                      }
                    }}
                    className={style.dependency_list_main_item_list_item}
                    onClick={_ => props.onDependencyClick(editor)}
                    key={editor.id}>
                    <div style={{
                      flexGrow: 1,
                      wordBreak: 'break-all',
                      padding: '3px 10px'
                    }}>
                      { editor.title ? editor.title : '<unnamed>' }
                    </div>
                    { !props.isReadOnly && <div 
                      style={{
                        display: 'flex',
                        position: 'relative',
                        top: 0,
                        alignItems: 'center',
                        margin: '0px 8px',
                        borderTopRightRadius: 999,
                        borderBottomRightRadius: 999
                      }} 
                      onClick={e => {
                        e.stopPropagation();
                        props.onRequestRemoveDependency(editor.id, props.parentID);
                      }}>
                      <img style={{height: 7, width: 7}} src={'/close.png'} />
                    </div> }
                  </div>
                ))
              }
            </div>
          </div>
        ))
      }
    </div>
  );
}