import * as React from 'react';
import BaseEditorData from '../main/base-editor/BaseEditorData';
import BaseEditor, { BaseEditorProps, baseMemoFunction } from './../main/base-editor/BaseEditor';

export interface EditorExternalModuleProps extends BaseEditorProps<BaseEditorData> {}

function EditorExternalModule(props: EditorExternalModuleProps) {
    return (
        <BaseEditor {...props}>
        </BaseEditor>
    );
}

export default React.memo(
  EditorExternalModule, 
  function (prevProps: Readonly<EditorExternalModuleProps>, nextProps: Readonly<EditorExternalModuleProps>): boolean {
      let result = baseMemoFunction<BaseEditorData>(prevProps, nextProps);
      return result;
  }
);
