import * as React from 'react';
import Draggable from 'react-draggable';
import _export_backend_expressjs from '../../export/export-backend-expressjs';
import _export_backend_php from '../../export/export-backend-php';
import _export_frontend_nextjs from '../../export/export-frontend-nextjs';
import BottomBar from '../bottom-bar/BottomBar';
import CollectionList from '../collection-list/CollectionList';
import SideBar from '../side-bar/SideBar';
import Toolbar from '../toolbar/Toolbar';
import BaseEditor, { BaseEditorProps } from './base-editor/BaseEditor';
import BaseEditorData from './base-editor/BaseEditorData';
import { LAMBDA_TYPE, MODEL_DATA } from './ComponentConstant';
import { CanvasProps, DEFAULT_CANVAS_STATE, download, EDITOR_WIDTH, getControllerRandomColor, getEditorInRangeByPoint, IDEFile, IDESheet, keyOf, NO_ACTIVE_EDITOR, NO_ACTIVE_SHEET, NO_DEPENDENCY_ACTIVE, NO_EDITOR, NO_MOVE_POSITION_YET, openFile, useThisStyles, } from './Constant';
import ContextMenu, { ContextMenuItem } from './context-menu/ContextMenu';
import DependencyLine from './dependency-line/DependencyLine';
import { DependencyLineData } from './dependency-line/DependencyLineData';
import { RegisterComponent } from './RegisterComponent';


// -------------------------------------------------
// SOMETIMES DO NOT USE STATE FOR PERFORMANCE REASON
// -------------------------------------------------

let sheet_counter = 1;

// flag mode menggeser canvas (click + drag move)
let is_mode_move_canvas = false;

// flag mode menggeser canvas dengan puter2 di tengah mouse atau dua jari di touchpad (wheel)
let is_mode_wheel_captured = false;

// id provider dependency line saat pertama kali drag
let id_temp_start_drag_editor = NO_EDITOR;

// posisi canvas terakhir sebelum diubah pada state [canvas_props, set]
let old_move_canvas_position = {
  x: NO_MOVE_POSITION_YET,
  y: NO_MOVE_POSITION_YET
}

// posisi mouse terakhir sebelum klik kanan
let context_menu_position = {
  x: 0,
  y: 0
}

export default function Application() {

  const style = useThisStyles();


  // -------------
  // Project State
  // -------------
  const [list_sheet, setListSheet] = React.useState<IDESheet[]>([]);
  const [id_active_sheet, setActiveSheet] = React.useState<number>(NO_ACTIVE_SHEET);


  // ------------------
  // Active Sheet State
  // ------------------

  const [canvas_props, setCanvasProps] = React.useState<CanvasProps>(DEFAULT_CANVAS_STATE);
  const [focus_active_id, setFocusActiveID] = React.useState<number>(NO_ACTIVE_EDITOR);
  const [is_context_menu_active, setContextMenuActive] = React.useState<boolean>(false);
  const [active_dependency_line_id, setActiveDependencyLineID] = React.useState<number>(NO_DEPENDENCY_ACTIVE);
  const [is_read_only, setReadOnly] = React.useState<boolean>(false);

  const [list_editor, setListEditor] = React.useState<BaseEditorData[]>([]);
  const [list_dependency, setDependency] = React.useState<DependencyLineData[]>([]);
  const [list_top_offset_dependency, setTopOffsetDependency] = React.useState<{[key: number]: number}>({});




  // ---------------------
  // Main Container Events
  // ---------------------

  // on left mouse click
  function onClick(event: React.MouseEvent<HTMLDivElement, MouseEvent>) {
    setContextMenuActive(false);
  }

  // on right mouse click
  function onContextMenu(event: React.MouseEvent<HTMLDivElement, MouseEvent>) {
    console.log('right mouse click');
    event.preventDefault();
    context_menu_position = {
      x: event.pageX,
      y: event.pageY
    };
    setContextMenuActive(true);
  }
  
  // on mouse wheel / two finger touch move
  function onWheel(event: React.WheelEvent<HTMLDivElement>) {
    // event.preventDefault();
    event.stopPropagation();
    is_mode_wheel_captured = true;
    // updateItemsCanvasPosition(event.shiftKey || event.ctrlKey, event.deltaX, event.deltaY, event.pageX, event.pageY);
    updateItemsCanvasPosition(false, event.deltaX, event.deltaY, event.pageX, event.pageY);
  }

  // on left mouse down
  function onTouchStart(event: React.MouseEvent<HTMLDivElement, MouseEvent>) {
    is_mode_move_canvas = !is_mode_move_canvas;
    if (is_mode_move_canvas) {
      setFocusActiveID(NO_ACTIVE_EDITOR);
    }
    if (event.shiftKey && id_temp_start_drag_editor !== NO_EDITOR) {
      const editor = list_editor.find((editor: BaseEditorData) => editor.id == id_temp_start_drag_editor);
      const new_line: DependencyLineData = {
        id: new Date().getTime(),
        id_provider: id_temp_start_drag_editor,
        id_consumer: NO_EDITOR,
        x_provider: event.pageX,
        y_provider: event.pageY,
        x_consumer: event.pageX,
        y_consumer: event.pageY,
        w_provider: editor?.w ?? 0,
        h_provider: editor?.h ?? 0,
        w_consumer: 0,
        h_consumer: 0,
        scale: canvas_props.scale,
        color: getControllerRandomColor()
      };
      setActiveDependencyLineID(new_line.id);
      setDependency([...list_dependency, new_line]);
    }
  }

  // on left mouse down is moving
  function onDependencyPointMoveDrag(event: React.MouseEvent<HTMLDivElement, MouseEvent>) {
    if (is_mode_move_canvas) {
      const dx = old_move_canvas_position.x === NO_MOVE_POSITION_YET ? 0 : old_move_canvas_position.x - event.pageX;
      const dy = old_move_canvas_position.y === NO_MOVE_POSITION_YET ? 0 : old_move_canvas_position.y - event.pageY;
      old_move_canvas_position = {
        x: event.pageX,
        y: event.pageY
      };
      
      const updated_list_editor = list_editor.map((editor: BaseEditorData) => {
        return {
          ...editor,
          x: editor.x - dx,
          y: editor.y - dy
        };
      });

      const updated_dependency_line = list_dependency.map((line: DependencyLineData) => {
        const editor_provider = updated_list_editor.find((e: BaseEditorData) => e.id === line.id_provider);
        const editor_consumer = updated_list_editor.find((e: BaseEditorData) => e.id === line.id_consumer);

        return {
          ...line,
          x_provider: editor_provider?.x ?? 0,
          y_provider: editor_provider?.y ?? 0,
          x_consumer: editor_consumer?.x ?? 0,
          y_consumer: editor_consumer?.y ?? 0
        };
      });

      setListEditor(updated_list_editor);
      setDependency(updated_dependency_line);
    }

    if (event.shiftKey) {
      const updated_dependency_line = list_dependency.map((line: DependencyLineData) => {
        if (line.id === active_dependency_line_id) {
          const editor_provider = list_editor.find((e: BaseEditorData) => e.id === line.id_provider);
          return {
            ...line,
            x_provider: editor_provider?.x ?? 1,
            y_provider: editor_provider?.y ?? 1,
            x_consumer: event.pageX,
            y_consumer: event.pageY
          }
        }

        return line;
      });
      setDependency(updated_dependency_line);
    }
  }

  // on left mouse up
  function onDependencyPointEndDrag(event: React.MouseEvent<HTMLDivElement, MouseEvent>) {
    is_mode_move_canvas = false;
    old_move_canvas_position = {
      x: NO_MOVE_POSITION_YET,
      y: NO_MOVE_POSITION_YET
    };

    if (active_dependency_line_id !== NO_DEPENDENCY_ACTIVE) {
      let updated_line_dependency: DependencyLineData[] = list_dependency;
      try {
        const editor_consumer = getEditorInRangeByPoint(list_editor, canvas_props.scale, event.pageX, event.pageY);
        if (editor_consumer.is_zero_dependency ?? false) {
          throw new Error('editor has zero dependency constraint');
        }
        if (editor_consumer && id_temp_start_drag_editor !== editor_consumer.id) {
          if (editor_consumer.dependencies.includes(id_temp_start_drag_editor)) {
            throw new Error('dependency exist');
          }
          updated_line_dependency = list_dependency.map((line: DependencyLineData) => {
            if (line.id === active_dependency_line_id) {
              return {
                ...line,
                id_consumer: editor_consumer?.id ?? NO_EDITOR,
                x_consumer: editor_consumer?.x ?? NO_MOVE_POSITION_YET,
                y_consumer: editor_consumer?.y ?? NO_MOVE_POSITION_YET,
                w_consumer: editor_consumer?.w ?? 0,
                h_consumer: editor_consumer?.h ?? 0
              }
            }
            return line;
          });
          setListEditor(list_editor.map((editor: BaseEditorData) => {
            if (editor.id === editor_consumer.id) {
              return {
                ...editor,
                dependencies: [...editor.dependencies, id_temp_start_drag_editor]
              }
            }
            return editor;
          }));
        }
      } catch (err) {
        //
      } finally {
        setDependency([...updated_line_dependency.filter((line: DependencyLineData) => line.id_consumer !== NO_EDITOR)]);
        setActiveDependencyLineID(NO_DEPENDENCY_ACTIVE);
        id_temp_start_drag_editor = NO_EDITOR;
      }
    }
  }

  // on left mouse double click
  function onDoubleClick(event: React.MouseEvent<HTMLDivElement, MouseEvent>) {
    is_mode_wheel_captured = false;
    const shiftKey = event.shiftKey;
    const pageX = event.pageX;
    const pageY = event.pageY;
    updateItemsCanvasPosition(true, 0, (shiftKey ? 50 : -100), pageX, pageY);
  }




  // ------------------
  // Item Editor Events
  // ------------------

  // Editor.onValueChange
  function handleValueChange(editor_id: number, kvs: {[key: string]: any}) {
    const updated_list_editor = list_editor.map((editor: BaseEditorData) => {
      if (editor.id === editor_id) {
        return { ...editor, ...kvs };
      }

      return editor;
    });

    const updated_dependency_line = list_dependency.map((line: DependencyLineData) => {
      const editor_provider = updated_list_editor.find((e: BaseEditorData) => e.id === line.id_provider);
      const editor_consumer = updated_list_editor.find((e: BaseEditorData) => e.id === line.id_consumer);

      return {
        ...line,
        x_provider: editor_provider?.x ?? 0,
        y_provider: editor_provider?.y ?? 0,
        x_consumer: editor_consumer?.x ?? 0,
        y_consumer: editor_consumer?.y ?? 0,
        w_provider: editor_provider?.w ?? 0,
        h_provider: editor_provider?.h ?? 0,
        w_consumer: editor_consumer?.w ?? 0,
        h_consumer: editor_consumer?.h ?? 0,
        scale: canvas_props.scale
      };
    });

    setListEditor(updated_list_editor);
    setDependency(updated_dependency_line);
  }

  // Editor.onMouseDown
  function itemIsMouseDown(editor_id: number) {
    is_mode_move_canvas = !is_mode_move_canvas;
    id_temp_start_drag_editor = editor_id;
    const max_z_index = Math.max(...list_editor.map((editor: BaseEditorData): number => editor.z_index));
    // console.log('itemIsMouseDown');

    // const updated_list_editor = list_editor.map((editor: BaseEditorData) => {
    //   if (editor_id === editor.id) {
    //     return {
    //       ...editor,
    //       z_index: max_z_index + (editor.z_index < max_z_index ? 1 : 0)
    //     };
    //   }
    //   return editor;
    // });

    setFocusActiveID(editor_id);
    // setListEditor(updated_list_editor);

    // will bubble to this.onTouchStart
  }

  // Editor.onRequestDelete
  function deleteItemEditor(editor_id: number) {
    setListEditor(list_editor.filter((editor: BaseEditorData) => editor.id !== editor_id));
    setDependency(list_dependency.filter((line: DependencyLineData) => line.id_consumer !== editor_id && line.id_provider !== editor_id));
  }

  // Editor.onTopOffsetDependecyChange
  function onTopOffsetDependecyChange(key: number, top_offset: number) {
    if (list_top_offset_dependency[key] && list_top_offset_dependency[key] === top_offset) {
      return;
    }
    const new_data = {
      ...list_top_offset_dependency,
      [key]: top_offset
    };
    setTopOffsetDependency(new_data);
  }

  // Editor.onRequestRemoveDependency
  function deleteLineDependency(id_provider: number, id_consumer: number) {
    setListEditor(list_editor.map((editor: BaseEditorData) => {
      if (editor.id === id_consumer) {
        return {
          ...editor,
          dependencies: [...editor.dependencies.filter((id_dependency_provider: number) => id_dependency_provider !== id_provider)]
        }
      }
      return editor;
    }))
    setDependency([...list_dependency.filter((line: DependencyLineData) => line.id_consumer !== id_consumer || line.id_provider !== id_provider)]);
  }




  // -----------------
  // Support Functions
  // -----------------

  // add new item to canvas
  function contextMenuAction(item: ContextMenuItem) {
    setContextMenuActive(false);
    const max_z_index = Math.max(...list_editor.map((editor: BaseEditorData): number => editor.z_index), 0);
    const data: BaseEditorData = item.initial_props({
        item_type: item.id,
        id: new Date().getTime(),
        x: context_menu_position.x,
        y: context_menu_position.y,
        w: EDITOR_WIDTH,
        h: 200,
        scale: canvas_props.scale,
        z_index: max_z_index + 1,
        title: '',
        description: '',
        dependencies: []
    });
    const result = [...list_editor, data];
    setListEditor(result);
  }

  // update all card & line position on canvas
  function updateItemsCanvasPosition(isShiftKey: boolean, deltaX: number, deltaY: number, pageX: number, pageY: number) {

    const old_scale = canvas_props.new_scale;
    const new_scale = old_scale - (isShiftKey ? deltaY : 0) * 0.01;

    const pointer_position = {
      x: pageX,
      y: pageY,
    }

    const dx = deltaX * 2;
    const dy = deltaY * 2;
    const dscale = new_scale - old_scale;
    const updated_scale = canvas_props.scale + canvas_props.scale * (new_scale - old_scale);

    // console.log(`native event x: ${pageX} y: ${pageY}`);
    // console.log(`delta position x: ${dx} y: ${dy} scale: ${dscale}`);

    const updated_list_editor = list_editor.map((editor: BaseEditorData) => {
      const d_zoom_x = (editor.x - pointer_position.x) * dscale;
      const d_zoom_y = (editor.y - pointer_position.y) * dscale;
      const d_width = editor.w * dscale;
      const d_height = editor.h * dscale;

      // console.log(`${editor.id} => innner scale: ${updated_scale.toFixed(4)} delta zoom x: ${d_zoom_x.toFixed(4)}, zoom y: ${d_zoom_y.toFixed(4)}, width: ${d_width.toFixed(4)}, height: ${d_height.toFixed(4)}`);

      return {
        ...editor,
        x: editor.x + d_zoom_x - (dscale === 0 ? dx : 0),
        y: editor.y + d_zoom_y - (dscale === 0 ? dy : 0),
        w: editor.w + d_width,
        h: editor.h + d_height,
        scale: updated_scale
      };
    });

    const updated_dependency_line = list_dependency.map((line: DependencyLineData) => {
      const editor_provider = updated_list_editor.find((e: BaseEditorData) => e.id === line.id_provider);
      const editor_consumer = updated_list_editor.find((e: BaseEditorData) => e.id === line.id_consumer);

      return {
        ...line,
        x_provider: editor_provider?.x ?? 0,
        y_provider: editor_provider?.y ?? 0,
        x_consumer: editor_consumer?.x ?? 0,
        y_consumer: editor_consumer?.y ?? 0,
        w_provider: editor_provider?.w ?? 0,
        h_provider: editor_provider?.h ?? 0,
        w_consumer: editor_consumer?.w ?? 0,
        h_consumer: editor_consumer?.h ?? 0,
        scale: canvas_props.scale
      };
    });

    setCanvasProps({
      ...canvas_props,
      new_scale,
      old_scale,
      scale: updated_scale,
      x: pointer_position.x,
      y: pointer_position.y
    });
    setListEditor(updated_list_editor);
    setDependency(updated_dependency_line);
  }

  function initNewSheet(): IDESheet {
    return {
      id: new Date().getTime(),
      name: `Sheet ${sheet_counter++}`,
      canvas_props: DEFAULT_CANVAS_STATE,
      list_editor: [],
      list_dependency: [],
      list_top_offset_dependency: {}
    }
  }

  function addNewSheet() {
    const new_sheet = initNewSheet();
    const updated_sheet_list = [...list_sheet, new_sheet];
    changeSheet(updated_sheet_list, new_sheet.id);
  }

  function removeSheet(id_sheet: number) {
    if (list_sheet.length === 1) {
      return;
    }
    const updated_list_sheet = list_sheet.filter((sheet: IDESheet) => sheet.id !== id_sheet);
    if (id_active_sheet === id_sheet) {
      changeSheet(updated_list_sheet, updated_list_sheet[(updated_list_sheet.length + 1) % updated_list_sheet.length].id);
    } else {
      setListSheet(updated_list_sheet);
    }
  }

  function changeSheet(_list_sheet: IDESheet[], id_sheet: number) {
    const sheet = _list_sheet.find((sheet: IDESheet) => sheet.id === id_sheet);
    if (!sheet) {
      return;
    }

    const updated_list_sheet = _list_sheet.map((_sheet: IDESheet) => {
      if (_sheet.id === id_active_sheet) {
        return {
          ..._sheet,
          canvas_props: {...canvas_props},
          list_editor: [...list_editor],
          list_dependency: [...list_dependency],
          list_top_offset_dependency: {...list_top_offset_dependency}
        }
      }

      return _sheet;
    });
    setListSheet(updated_list_sheet);

    if (id_active_sheet !== NO_ACTIVE_SHEET) {
      setDependency(sheet.list_dependency);
      setListEditor(sheet.list_editor);
      setTopOffsetDependency(sheet.list_top_offset_dependency);
      setCanvasProps(sheet.canvas_props);
    }
    setActiveSheet(id_sheet);
  }

  function changeSheetName(id_sheet: number, name: string) {
    const updated_list_sheet = list_sheet.map((sheet: IDESheet) => {
      if (sheet.id === id_sheet) {
        return {
          ...sheet,
          name
        }
      }
      return sheet;
    });
    setListSheet(updated_list_sheet);
  }

  function load() {
    try {
      openFile((stringified_json: string) => {
        try {
          const ide_file: IDEFile = JSON.parse(stringified_json);
          if (ide_file.data.length > 0) {
            changeSheet(ide_file.data, ide_file.data[0].id);
          } else {
            addNewSheet();
          }
        } catch (err) {
          alert(err);
        }
      });
    } catch (err) {
      alert(err);
    }
  }

  function save() {
    const ide_file: IDEFile = {
      timestamp: new Date().getTime(),
      data: list_sheet.map((_sheet: IDESheet) => {
        if (_sheet.id === id_active_sheet) {
          return {
            ..._sheet,
            canvas_props: {...canvas_props},
            list_editor: [...list_editor],
            list_dependency: [...list_dependency],
            list_top_offset_dependency: {...list_top_offset_dependency}
          }
        }
  
        return _sheet;
      })
    };
    const download_data = JSON.stringify(ide_file);
    const filename = `ide-${new Date().getTime()}.json`;
    download(download_data, filename, 'json');
  }

  function about() {
    alert('ProjectX');
  }

  function moveToEditor(editor: BaseEditorData) {
    const current_x = editor.x - window.innerWidth / 2 + editor.w / 2 + 125;
    const current_y = editor.y - window.innerHeight / 2 + editor.w / 2;
    is_mode_wheel_captured = false;

    const updated_list_editor = list_editor.map(_editor => ({
      ..._editor,
      x: _editor.x - current_x,
      y: _editor.y - current_y
    }));

    const updated_dependency_line = list_dependency.map((line: DependencyLineData) => {
      const editor_provider = updated_list_editor.find((e: BaseEditorData) => e.id === line.id_provider);
      const editor_consumer = updated_list_editor.find((e: BaseEditorData) => e.id === line.id_consumer);

      return {
        ...line,
        x_provider: editor_provider?.x ?? 0,
        y_provider: editor_provider?.y ?? 0,
        x_consumer: editor_consumer?.x ?? 0,
        y_consumer: editor_consumer?.y ?? 0,
        w_provider: editor_provider?.w ?? 0,
        h_provider: editor_provider?.h ?? 0,
        w_consumer: editor_consumer?.w ?? 0,
        h_consumer: editor_consumer?.h ?? 0,
      };
    });

    setListEditor(updated_list_editor);
    setDependency(updated_dependency_line);

    setFocusActiveID(editor.id);
  }

  function stopRenderingEditor<T extends BaseEditorData>(editor: T): boolean {
    if (editor.x + editor.w < 0) {
        return true;
    }
    if (editor.y + editor.h < 0) {
        return true;
    }
    if (editor.x > (window.innerWidth ?? 0)) {
        return true;
    }
    if (editor.y > (window.innerHeight ?? 0)) {
        return true;
    }
    return false;
  }

  React.useEffect(() => {
    addNewSheet();
  }, []);

  const filtered_id_list_editor_model_data_or_lambda = list_editor
    .filter((editor: BaseEditorData) => editor.item_type === MODEL_DATA || editor.item_type === LAMBDA_TYPE)
    .map((editor: BaseEditorData) => editor.id);

  const editor_focus = list_editor.find((editor: BaseEditorData) => editor.id === focus_active_id);

  // console.log('rendering');

  return (
    <div
      onClick={onClick}
      onContextMenu={onContextMenu}
      className={style.application_container}
      onWheel={onWheel}
      onMouseDown={onTouchStart}
      onMouseMove={onDependencyPointMoveDrag}
      onMouseUp={onDependencyPointEndDrag}
      onDoubleClick={onDoubleClick}>
      <div style={{
        overflow: 'hidden'
      }}>
        {
          list_dependency.filter((line: DependencyLineData) => {
            if (line.id_consumer === NO_EDITOR) {
              return true;
            }
            const is_not_flow_data = !filtered_id_list_editor_model_data_or_lambda.includes(line.id_provider);
            return is_not_flow_data;
          }).map((line: DependencyLineData) => (
            <DependencyLine
              key={line.id}
              data={line}
              isActive={line.id_provider === focus_active_id || line.id_consumer === focus_active_id}
              offsetTopConsumerDependency={list_top_offset_dependency[keyOf(line.id_consumer, line.id_provider)] ?? 0}
              innerWindowWidth={window.innerWidth}
              innerWindowHeight={window.innerHeight} />
          )).filter((line) => !!line)
        }
        {
          list_editor
          .filter((editor: BaseEditorData) => !stopRenderingEditor(editor))
          .map((editor: BaseEditorData) => {
            const item = RegisterComponent.find((context_menu: ContextMenuItem) => context_menu.id === editor.item_type);
            const ui = item.model({
              data: editor,
              isFocus: editor.id === focus_active_id,
              isConsumerFocus: editor_focus?.dependencies.includes(editor.id) ?? false,
              isReduceOpacity: editor.id === focus_active_id || focus_active_id === NO_ACTIVE_EDITOR,
              disabled: active_dependency_line_id !== NO_DEPENDENCY_ACTIVE,
              scale: canvas_props.scale,
              isCanvasMoving: is_mode_move_canvas || is_mode_wheel_captured,
              listEditor: list_editor,
              innerWindowWidth: window.innerWidth,
              innerWindowHeight: window.innerHeight,
              isReadOnly: is_read_only,
              onValueChange: (kvs: {[key: string]: any}) => active_dependency_line_id === NO_DEPENDENCY_ACTIVE && handleValueChange(editor.id, kvs),
              onMouseDown: () => itemIsMouseDown(editor.id),
              onMouseUp: () => {},
              onRequestDelete: () => deleteItemEditor(editor.id),
              onTopOffsetDependecyChange: onTopOffsetDependecyChange,
              onRequestRemoveDependency: deleteLineDependency,
              onDependencyClick: moveToEditor
            });
            return ui ? <div key={editor.id}>{ ui }</div> : null;
          })
        }
      </div>
      {
        (is_context_menu_active && !is_read_only) && 
        <div 
          onClick={e => e.stopPropagation()}
          onKeyDown={undefined}
          role="menu"
          tabIndex={0}
          className={style.application_context_menu}
          style={{
            left: context_menu_position.x,
            top: context_menu_position.y
          }}>
          <ContextMenu 
            items={RegisterComponent} 
            onMenuClick={contextMenuAction} 
          />
        </div>
      }
      <div className={style.application_toolbar}>
        <Toolbar items={[{
          id: 1,
          label: 'File',
          subitems: [{
            id: 2,
            label: 'Open...',
            action: load
          }, {
            id: 3,
            label: 'Download',
            action: save
          }]
        }, {
          id: 4,
          label: 'Export',
          subitems: [{
            id: 6,
            label: 'Frontend (NextJS)',
            action: () => {
              _export_frontend_nextjs(list_sheet.map((_sheet: IDESheet) => {
                if (_sheet.id === id_active_sheet) {
                  return {
                    ..._sheet,
                    canvas_props: {...canvas_props},
                    list_editor: [...list_editor],
                    list_dependency: [...list_dependency],
                    list_top_offset_dependency: {...list_top_offset_dependency}
                  }
                }
          
                return _sheet;
              }), file => {
                download(file, 'starter-next-js.zip', 'zip');
              })
            }
          }, {
            id: 5,
            label: 'Backend (ExpressJS)',
            action: () => {
              _export_backend_expressjs(list_sheet.map((_sheet: IDESheet) => {
                if (_sheet.id === id_active_sheet) {
                  return {
                    ..._sheet,
                    canvas_props: {...canvas_props},
                    list_editor: [...list_editor],
                    list_dependency: [...list_dependency],
                    list_top_offset_dependency: {...list_top_offset_dependency}
                  }
                }
          
                return _sheet;
              }), file => {
                download(file, 'starter-express-js.zip', 'zip');
              })
            }
          }, {
            id: 6,
            label: 'Backend (LaravelPHP)',
            action: () => {
              _export_backend_php(list_sheet.map((_sheet: IDESheet) => {
                if (_sheet.id === id_active_sheet) {
                  return {
                    ..._sheet,
                    canvas_props: {...canvas_props},
                    list_editor: [...list_editor],
                    list_dependency: [...list_dependency],
                    list_top_offset_dependency: {...list_top_offset_dependency}
                  }
                }
          
                return _sheet;
              }), file => {
                download(file, 'starter-laravel-php.zip', 'zip');
              })
            }
          }]
        }, {
          id: 8,
          label: 'Help',
          subitems: [{
            id: 9,
            label: 'About Project',
            action: about
          }]
        }]} />
      </div>

      <div style={{
        position: 'absolute',
        left: 0,
        bottom: 0,
        zIndex: 999999
      }}>
        <BottomBar
          listSheet={list_sheet.map((sheet: IDESheet) => ({
            id_sheet: sheet.id,
            title: sheet.name,
            is_active: sheet.id === id_active_sheet
          }))}
          onChangeSheetName={changeSheetName}
          onRequestRemoveSheet={id => {
            console.log(`remove ${id}`)
            removeSheet(id);
          }}
          onRequestChangeSheet={(id_sheet: number) => {
            if (id_sheet === id_active_sheet) {
              return;
            }
            changeSheet(list_sheet, id_sheet)
          }}
          onRequestNewSheet={() => addNewSheet()}
          setReadOnly={() => {
            setReadOnly(!is_read_only);
          }}
          isReadOnly={is_read_only} />
      </div>

      <div style={{
        position: 'absolute',
        left: 12,
        bottom: 0,
        height: '100%',
        zIndex: 9998,
        display: 'flex',
        alignItems: 'center'
      }}>
        <SideBar
          items={[{
            id: 1,
            icon_url: '/zoom-in.png',
            label: 'Zoom In',
            action() {
              is_mode_wheel_captured = false;
              updateItemsCanvasPosition(true, 0, -15, window.innerWidth / 2, window.innerHeight / 2);
            }
          }, {
            id: 2,
            icon_url: '/zoom-out.png',
            label: 'Zoom Out',
            action() {
              is_mode_wheel_captured = false;
              updateItemsCanvasPosition(true, 0, 15, window.innerWidth / 2, window.innerHeight / 2);
            }
          }]} />
      </div>

      <div style={{
        position: 'absolute',
        right: 0,
        top: 0,
        height: '100%',
        zIndex: 99999,
      }}>
        <CollectionList 
          listEditor={list_editor}
          activeEditorID={focus_active_id}
          onEditorClick={moveToEditor} />
      </div>

    </div>
  );
}
