import * as React from 'react';
import { NO_EDITOR } from '../Constant';
import { DependencyLineData } from './DependencyLineData';

export interface DependencyLineProps {
  data: DependencyLineData
  isActive: boolean
  offsetTopConsumerDependency: number
  innerWindowWidth: number
  innerWindowHeight: number
}

type Quadrant = (1 | 2 | 3 | 4);

export default function DependencyLine(props: DependencyLineProps) {

  function findObjectQuadrantOf(obj_x: number, obj_y: number, zero_point_x: number, zero_point_y: number): Quadrant {
    if (obj_x > zero_point_x) {
      return obj_y > zero_point_y ? 4 : 1;
    } else {
      return obj_y > zero_point_y ? 3 : 2;
    }
  }

  const offset_dependency = props.data.id_consumer ? props.offsetTopConsumerDependency * props.data.scale : 0;

  const provider_quadrant = findObjectQuadrantOf(
    props.data.x_provider,
    props.data.y_provider,
    props.data.x_consumer,
    props.data.y_consumer
  );

  const x1 = provider_quadrant === 2 || provider_quadrant === 3 ? props.data.x_provider : props.data.x_consumer;
  const y1 = x1 === props.data.x_provider ? props.data.y_provider : props.data.y_consumer + offset_dependency;
  const w1 = x1 === props.data.x_provider ? props.data.w_provider : props.data.w_consumer;

  const x2 = provider_quadrant === 2 || provider_quadrant === 3 ? props.data.x_consumer : props.data.x_provider;
  const y2 = x2 === props.data.x_provider ? props.data.y_provider : props.data.y_consumer + offset_dependency;

  const additional_w = (x1 + w1) > x2 ? 0 : w1;
  const additional_h = 0;

  const w = Math.abs(x2 - x1) - additional_w;
  const h = Math.abs(y2 - y1) - additional_h;

  const quadrant_left_object = findObjectQuadrantOf(x1, y1, x2, y2);

  const calculated_left = x1 + additional_w;
  const calculated_top = Math.min(y1, y2);

  function stopRendering(): boolean {
    if (calculated_left + (w ?? 0) < 0) {
      return true;
    }
    if (calculated_top + (h ?? 0) < 0) {
      return true;
    }
    if (calculated_left > (props.innerWindowWidth ?? 0)) {
      return true;
    }
    if (calculated_top > (props.innerWindowHeight ?? 0)) {
      return true;
    }
    return false;
  }

  if (stopRendering()) {
    return null;
  }

  return (
    <svg 
      height={h}
      width={w}
      style={{
        position: 'absolute',
        zIndex: (props.isActive ? 10000 : 1) ?? 1,
        transform: `translate(${calculated_left}px,${calculated_top}px)`
      }}>
      { quadrant_left_object === 2 && <path 
        d={`M${0},${0} C${w / 2},${0} ${w / 2},${(h)} ${w},${(h)}`}
        style={{ stroke: props.data.color, opacity: (props.isActive ? .8 : .15) ?? .15, strokeWidth: 2, zIndex: 1, fill: 'none' }} /> }
      { quadrant_left_object !== 2 && <path 
        d={`M${0},${h} C${w / 2},${h} ${w / 2},${(0)} ${w},${(0)}`}
        style={{ stroke: props.data.color, opacity: (props.isActive ? .8 : .15) ?? .15, strokeWidth: 2, zIndex: 1, fill: 'none' }} /> }
    </svg>
  );
}
