function getDataType(list_editor: BaseEditorData[], data_type: VariableType) {
  if (data_type.type_parent_id === NO_VARIABLE_TYPE_PARENT) {
    return data_type.type_name === 'function' ? '(_params: any) => {}' : data_type.type_name;
  }
  const editor = list_editor.find(x => x.id === data_type.type_parent_id)!;
  if (editor.item_type === LAMBDA_TYPE) {
    return `(${(editor as EditorLambdaTypeData).parameters.map((param: ModelVariable) => {
      return `${param.name}: ${param.type.type_name}`
    }).join(', ')}) => ${(editor as EditorLambdaTypeData).return_type.type_name}`
  }
  return data_type.type_name;
}

const exporter: {[key: number]: ExportData } = {
  [EXTERNAL_MODULE]: {
    exportPath: 'external',
    exportExtension: 'ts',
    exportToFile(editor: BaseEditorData, all_editor: BaseEditorData[]) {
      return `// implement your external module below\nexport const ${editor.title} = null;`;
    }
  },
  [PRESENTATION_UI]: {
    exportPath: 'pages',
    exportExtension: 'tsx',
    exportToFile(editor: EditorUIData, all_editor: BaseEditorData[]) {
      const react_import = `import * as React from 'react';`;
      const parameters = 'props: {' + editor.list_ui_parameter.map((param: ModelVariable) => `${param.name}${param.required ? '' : '?'}: ${getDataType(all_editor, param.type)}`).join(', ') + '}';
      const states = editor.list_ui_state.map((state: UIState) => `  const [${state.name}, set${state.name}] = React.useState<${getDataType(all_editor, state.type)}>();`).join('\n');
      return `${react_import}\nimport { useRouter } from 'next/dist/client/router';\n\nexport default function ${editor.title}(${parameters}) {\n  const router = useRouter();\n\n${states}\n\n  // implement here...\n  return <div></div>\n}`;
    }
  },
  [PRESENTATION_UI_COMPONENTS]: {
    exportPath: 'components',
    exportExtension: 'tsx',
    exportToFile(editor: EditorUIData, all_editor: BaseEditorData[]) {
      const react_import = `import * as React from 'react';`;
      const parameters = 'props: {' + editor.list_ui_parameter.map((param: ModelVariable) => `${param.name}${param.required ? '' : '?'}: ${getDataType(all_editor, param.type)}`).join(', ') + '}';
      const states = editor.list_ui_state.map((state: UIState) => `  const [${state.name}, set${state.name}] = React.useState<${getDataType(all_editor, state.type)}>();`).join('\n');
      return `${react_import}\n\nexport function ${editor.title}(${parameters}) {\n${states}\n\n  // implement here...\n  return <div></div>\n}`;
    }
  },
  [BUSINESS_LOGIC]: {
    exportPath: 'logic',
    exportExtension: 'ts',
    exportToFile(editor: EditorBusinessLogicData, all_editor: BaseEditorData[]) {
      const parameters = 'params: {' + editor.parameters.map((param: ModelVariable) => `${param.name}${param.required ? '' : '?'}: ${getDataType(all_editor, param.type)}`).join(', ') + '}';
      return `export async function ${editor.title}(${parameters}): Promise<${getDataType(all_editor, editor.return_type)}> {\n  // implement here...\n  throw new Error('function ${editor.title} is not implemented yet');\n}`;
    }
  },
  [DATA_ACCESS]: {
    exportPath: 'access-data',
    exportExtension: 'ts',
    exportToFile(editor: EditorDataAccessData, all_editor: BaseEditorData[]) {
      const parameters = 'params: {' + editor.parameters.map((param: ModelVariable) => `${param.name}${param.required ? '' : '?'}: ${getDataType(all_editor, param.type)}`).join(', ') + '}';
      return `export async function ${editor.title}(${parameters}): Promise<${getDataType(all_editor, editor.return_type)}> {\n  // implement here...\n  throw new Error('function ${editor.title} is not implemented yet');\n}`;
    }
  },
  [API_ACCESS]: {
    exportPath: 'access-api',
    exportExtension: 'ts',
    exportToFile(editor: EditorAPIAccessData, all_editor: BaseEditorData[]) {
      const real_url = editor.variables
        .filter((variable: APIVariableData) => variable.kind === 'P')
        .reduce((acc_url: string, variable: APIVariableData) => {
          return acc_url.replace(`:${variable.name}`, `\$\{params.${variable.name}\}`);
        }, editor.url);
      const parameters = 'params: {' + editor.variables.map((param: APIVariableData) => `${param.name}${param.required ? '' : '?'}: ${getDataType(all_editor, param.type)}`).join(', ') + '}';
      const non_params = editor.variables.filter((variable: APIVariableData) => variable.kind !== 'P');
      const bodies = 'const bodies = {\n' + non_params.map((param: APIVariableData) => `    ${param.name}: params.${param.name}`).join(',\n') + '\n  }';
      return `import API, { APIResponse } from '../api';\n\nexport async function ${editor.title}(${parameters}): Promise<${getDataType(all_editor, editor.return_type)}> {\n  ${bodies}\n\n  try {\n    return (await API(\`${real_url}\`, '${editor.method}', bodies) as APIResponse<${getDataType(all_editor, editor.return_type)}>).data;\n  } catch (err) {\n    throw err;\n  }\n}`;
    }
  },
  [MODEL_DATA]: {
    exportPath: 'data-model',
    exportExtension: 'ts',
    exportToFile(editor: EditorDataModelData, all_editor: BaseEditorData[]) {
      const attributes = editor.attributes.map((param: ModelVariable) => `  ${param.name}${param.required ? '' : '?'}: ${getDataType(all_editor, param.type)}`).join('\n');
      return `interface ${editor.title} {\n${attributes}\n}\n\nexport type { ${editor.title} }`;
    }
  }
};

