const api = `export const BASE_URL = process.env.MODE === 'dev' ? process.env.DEV_BACKEND : process.env.PROD_BACKEND;

export interface APIResponse<T> {
  status: boolean
  data: T
}

export default function API(path, method, body = {}, header = {}, base_url?: string) {
    return new Promise((resolve, reject) => {
        fetch(\`\$\{base_url ?? BASE_URL\}\$\{path\}\$\{ method == 'get' ? '?' + new URLSearchParams(body).toString() : '' \}\`, {
            method,
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                ...header
            },
            body: method == 'get' ? null : JSON.stringify(body)
        })
        .then(res => {
            if (res.ok) {
                resolve(res.json());
                return false;
            }
            throw res.json();
        })
        .catch(err => {
            if (!err || !err.then) {
                reject('Something went wrong');
                return;
            }
            err.then(({ error }) => {
                reject(error);
            })
        });
    });
}
`;

