function importDependency<T extends BaseEditorData>(_list_editor: T[], editor: T): string {
  const this_item_context_menu = exporter[editor.item_type];
  if (!this_item_context_menu) {
    throw new Error(`Context Menu editor: ${editor.title} tidak ditemukan`);
  }

  const folder_depth = (this_item_context_menu.exportPath.match(/\//g) || []).length + 1;
  
  const list_editor_available = _list_editor.filter((editor: BaseEditorData) => exporter[editor.item_type]);
  const list_editor_available_ids = list_editor_available.map(x => x.id);
  const list_editor_lambda = _list_editor.filter((editor: BaseEditorData) => editor.item_type === LAMBDA_TYPE).map((editor: BaseEditorData) => editor.id);
  console.log(list_editor_lambda);
  
  return `${
    editor.dependencies
    .filter((dep_id: number) => list_editor_available_ids.includes(dep_id)) // filter only dependency non lambda
    .map((id_dependency: number) => {
      const x = list_editor_available.find((ed: T) => ed.id === id_dependency)
      if (!x) {
        console.log('nulll: ', id_dependency);
      }
      return x;
    }) // map to editor instance
    .map((_editor: T) => {
      const item_context_menu = exporter[_editor.item_type];
      return `import { ${_editor.title} } from '${'../'.repeat(folder_depth)}${item_context_menu.exportPath}/${_editor.title}'`
    }).join("\n")
  }\n${
    editor.dependencies
    .filter((dep_id: number) => list_editor_lambda.includes(dep_id)) // filter only dependency lambda
    .map((dep_id: number) => _list_editor.find((ed: T) => ed.id === dep_id)!) // map to editor instance
    .reduce((acc, curr) => {
      // remove all lambda dependency
      const dep_non_lambda = curr.dependencies.filter((ed_id: number) => !list_editor_lambda.includes(ed_id));
      // skip if existing on accumulator reducer or this editor dependencies
      const dep_remove_existing_on_accumulator = dep_non_lambda.filter((ed_id: number) => !acc.includes(ed_id) && !editor.dependencies.includes(ed_id));
      // return all as breaking array
      return [...acc, ...dep_remove_existing_on_accumulator];
    }, [])
    .map((id_dependency: number) => list_editor_available.find((ed: T) => ed.id === id_dependency)!) // map to editor instance
    .map((_editor: T) => {
      const item_context_menu = exporter[_editor.item_type];
      return `import { ${_editor.title} } from '${'../'.repeat(folder_depth)}${item_context_menu.exportPath}/${_editor.title}'`
    }).join("\n")
  }`;
}

function createFile<T extends BaseEditorData>(editor: T, all_editor: T[]): string {
  const item = exporter[editor.item_type];
  return item.exportToFile(editor, all_editor);
}

export default function _export_frontend_nextjs(list_sheet: IDESheet[], callback: (file: Blob) => void) {
  const jszip = new JSZip();
  const all_editor: BaseEditorData[] = list_sheet.reduce((acc: BaseEditorData[], current: IDESheet) => {
    return [...acc, ...current.list_editor];
  }, []);

  jszip.file('.env', dotEnv);
  jszip.file('.gitignore', gitignore);
  jszip.file('next-env.d.ts', nextEnvD);
  jszip.file('next.config.js', nextConfig);
  jszip.file('strings.ts', sampleString);
  jszip.file('theme.ts', sampleTheme);
  jszip.file('tsconfig.json', tsConfig);
  jszip.file('package.json', packageJSON);
  jszip.file('api.ts', api);
  jszip.file('pages/_app.css', appCSS);
  jszip.file('pages/_app.tsx', appTsx);
  jszip.file('pages/_document.tsx', documentTsx);
  jszip.file('pages/404.tsx', notfound404);
  jszip.file('hooks/useString.ts', useString);

  for (let i = 0; i < all_editor.length; i++) {
    const editor = all_editor[i];
    if (!exporter[editor.item_type]) {
      continue;
    }

    const item_context_menu = exporter[editor.item_type];
    const dependency_lines = importDependency(all_editor, editor);

    const file_content = `${dependency_lines ? `${dependency_lines}\n\n` : ''}${createFile(editor, all_editor)}\n`;
    jszip.file(`${item_context_menu.exportPath}/${editor.title}.${item_context_menu.exportExtension}`, file_content);
  }
  jszip.generateAsync({type:"blob"}).then(callback);
}
