import * as React from 'react';
import BaseEditorData from './BaseEditorData';
import Draggable, { DraggableEvent, DraggableData, DraggableEventHandler } from 'react-draggable';
import HeaderEditor from './HeaderEditor';
import DependencyList from './DependencyList';
import { RegisterComponent } from '../RegisterComponent';
import { ContextMenuItem } from '../context-menu/ContextMenu';
import { useThisStyles } from '../Constant';

export interface BaseEditorProps<T extends BaseEditorData> {
  data: T
  isFocus: boolean
  isConsumerFocus: boolean
  disabled: boolean
  listEditor: BaseEditorData[]
  isReduceOpacity: boolean
  isCanvasMoving: boolean
  scale: number
  innerWindowWidth: number
  innerWindowHeight: number
  children: JSX.Element | JSX.Element[]
  isReadOnly: boolean
  onRequestDelete(): void
  onValueChange(kvs: {[key: string]: any}): void
  onMouseDown(): void
  onMouseUp(): void
  onTopOffsetDependecyChange(key: string, top_offset: number): void
  onRequestRemoveDependency(id_provider: number, id_consumer: number): void
  onDependencyClick(editor: BaseEditorData): void
}

let on_drag = false;

export default function BaseEditor(props: BaseEditorProps<BaseEditorData>) {

  const style = useThisStyles();
  const [main_editor_ref] = [React.useRef(null)];

  function handleMouseDown(e: MouseEvent) {
    props.onMouseDown();
  }

  function handleStart(e: DraggableEvent, position: DraggableData): any {
    if (props.isReadOnly) {
      e.preventDefault();
      return false;
    }
    // no need to handle this event
    on_drag = true;
  }

  function handleDrag(e: DraggableEvent, position: DraggableData): any {
    if (props.isReadOnly || props.disabled) {
      return false;
    }
    props.onValueChange({
      x: position.x, 
      y: position.y
    });
  }

  function handleStop(e: DraggableEvent, position: DraggableData): any {
    if (props.isReadOnly) {
      e.preventDefault();
      return false;
    }
    // no need to handle this event
    on_drag = false;
  }

  React.useEffect(() => {
    const w = (main_editor_ref.current as any)?.getBoundingClientRect().width;
    const h = (main_editor_ref.current as any)?.getBoundingClientRect().height;
    props.onValueChange({ w, h });
  }, [
    (main_editor_ref.current as any)?.getBoundingClientRect().height
  ]);

  React.useEffect(() => {
    const w = (main_editor_ref.current as any)?.getBoundingClientRect().width;
    const h = (main_editor_ref.current as any)?.getBoundingClientRect().height;
    props.onValueChange({ w, h });
  }, []);

  const context_menu_item = RegisterComponent.find((cm: ContextMenuItem) => cm.id === props.data.item_type);

  return (
    <Draggable
      position={{
        x: props.data.x, 
        y: props.data.y
      }}
      disabled={props.disabled || props.isReadOnly}
      onMouseDown={handleMouseDown}
      onStart={handleStart}
      onDrag={handleDrag}
      onStop={handleStop}>
      <div style={{
        position: 'absolute', 
        transition: on_drag || props.isCanvasMoving ? '' : 'transform 250ms',
        width: props.data.w,
        height: props.data.h,
        zIndex: props.data.z_index,
        opacity: props.isReduceOpacity || props.isConsumerFocus ? 1 : .4,
      }}>
        <div 
          onDoubleClick={e => { e.preventDefault(); e.stopPropagation(); }} 
          ref={main_editor_ref}
          className={style.card_container}
          style={{
            background: context_menu_item.point_color,
            color: 'white',
            transition: on_drag || props.isCanvasMoving ? '' : 'transform 250ms',
            transform: `scale(${props.data.scale})`,
            boxShadow: props.isFocus ? '0px 3px 20px rgba(0, 0, 0, .35)' : '', 
            borderColor: props.isFocus ? 'white' : context_menu_item.point_color,
          }}>
            <HeaderEditor
              isReadOnly={props.isReadOnly}
              title={props.data.title}
              description={props.data.description}
              setTitle={(title: string) => props.onValueChange({ title })}
              setDescription={(description: string) => props.onValueChange({ description })}
              onRequestDelete={() => props.onRequestDelete()} />
            <DependencyList
              isReadOnly={props.isReadOnly}
              parentID={props.data.id}
              dependencies={props.data.dependencies}
              listEditor={props.listEditor}
              onTopOffsetDependecyChange={props.onTopOffsetDependecyChange}
              onRequestRemoveDependency={props.onRequestRemoveDependency}
              onDependencyClick={props.onDependencyClick} />
            { props.children }
            <div style={{
              fontSize: 8,
              padding: '6px 12px',
              display: 'flex',
              flexDirection: 'row',
              alignItems: 'center'
            }}>
              {/* <div style={{
                background: '#151515',
                width: '100%',
                height: 100,
                borderRadius: 8,
                boxShadow: 'inset 0px -1px 5px 2px rgba(0, 0, 0, .3)'
              }}></div> */}
              {/* <div style={{
                width: 8,
                height: 8,
                borderRadius: 8,
                marginTop: -2,
                background: context_menu_item?.point_color ?? '#999',
                marginRight: 8
              }} />
              <div style={{
                color: context_menu_item?.point_color ?? '#999',
              }}>
                { context_menu_item?.label ?? "-" }
              </div> */}
            </div>
            {/* <div style={{ whiteSpace: 'pre-wrap', fontSize: 8, background: '#EEE' }}>
              { JSON.stringify(props.data, null, 2) }
            </div> */}
        </div>
      </div>
    </Draggable>
  );
}

export function baseMemoFunction<T extends BaseEditorData>(prevProps: Readonly<BaseEditorProps<T>>, nextProps: Readonly<BaseEditorProps<T>>): boolean {
  // console.log(JSON.stringify(prevProps.data, null, 2), JSON.stringify(nextProps.data, null, 2));
  if (
    prevProps.data.title === nextProps.data.title
    && prevProps.data.description === nextProps.data.description
    && prevProps.data.z_index === nextProps.data.z_index
    && prevProps.data.scale === nextProps.data.scale
    && prevProps.data.x === nextProps.data.x
    && prevProps.data.y === nextProps.data.y
    && prevProps.data.h === nextProps.data.h
    && prevProps.data.w === nextProps.data.w
    && prevProps.data.dependencies.length === nextProps.data.dependencies.length
    && prevProps.isFocus === nextProps.isFocus
    && prevProps.disabled === nextProps.disabled
    && prevProps.isReduceOpacity === nextProps.isReduceOpacity
    && prevProps.listEditor.length === nextProps.listEditor.length
    && prevProps.isCanvasMoving === nextProps.isCanvasMoving
    && prevProps.isReadOnly === nextProps.isReadOnly
    && prevProps.isConsumerFocus === nextProps.isConsumerFocus
    && prevProps.scale === nextProps.scale // <-- hack for scale change
  ) {
    return true;
  }
  return false;
};
